import BoosterController from "./BoosterController";
const { ccclass, property } = cc._decorator;

export enum DebugType {
    Normal = 0,
    WinMutil = 1,
    BetReward = 2,
    FreeGame = 3,
    AllWild = 4

}
@ccclass
export default class DebugManager extends cc.Component {

    DebugType: number = 0;
    ChangeMod_Button: cc.Button = null;
    Display_current_Mod: cc.Label = null;

    private m_type: number = 0;
    m_ModeName: string[] = [
        'Normal\nMode',
        'WinMulti\nMode',
        'BetReward\nMode',
        'FreeGame\nMode',
        'AllWild\nMode',
    ]
    static DebugType: number;
    OverWritedata(): number {
        let result;
        switch (this.DebugType) {
            case 0:
                result = this.NormalMode();
                break;
            case 1:
                result = this.WinMultiMode();
                break;
            case 2:
                result = this.BetRewardMode();
                break;
            case 3:
                result = this.FreeGameMode();
                break;
            case 4:
                result = this.AllWildMode();
                break;
        }
        return result;

    }

    start() {
        this.init()
    }

    init() {

        this.ChangeMod_Button = this.node.getComponentInChildren(cc.Button)
        this.Display_current_Mod = this.node.getComponentInChildren(cc.Label)
        // GameManager.GM.bosCtr.getComponent(BoosterController)
        // cc.log(this.bosCtrl)

        // DebugManager.self = this.m_self;

        this.ChangeMod_Button.node.on('click', () => {
            this.m_type += 1;
            this.DebugType = this.m_type % 5;
            this.Display_current_Mod.string = this.m_ModeName[this.DebugType]
            cc.log(this.DebugType)

        })
    }

    NormalMode(): number {
        return -1
    }
    WinMultiMode(): number {
        return this.RandomRangeRound(3, 5)
    }
    BetRewardMode(): number {
        return this.RandomRangeRound(6, 9)
    }
    FreeGameMode(): number {
        return this.RandomRangeRound(0, 2)
    }
    AllWildMode(): number {
        return 10
    }
    RandomRangeRound(min: number, max: number) {
        return Math.round(Math.random() * (max - min) + min);
    }

}
