import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";
import CommonUI_ViewController from "./CommonUI_ViewController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Bonus_ViewController extends CommonUI_ViewController {


    @property(cc.Node)
    Bonus: cc.Node = null;
    @property(cc.Node)
    Bonus_hori: cc.Node = null;
    @property(cc.Node)
    Bonus_Verti: cc.Node = null;


    start() {
        this.ingoreNodeName = ['Score']
        EventSystem.on(EventType.ScreenChange, () => {
            if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
                this.Bonus.setPosition(this.Bonus_hori.getPosition(cc.v2()))
                this.Bonus.setScale(this.Bonus_hori.getScale(cc.v2()))
            }
            else {
                this.Bonus.setPosition(this.Bonus_Verti.getPosition(cc.v2()))
                this.Bonus.setScale(this.Bonus_Verti.getScale(cc.v2()))
            }
        })
    }

    other_commonUI_PanleLayoutSetting = () => {


    }
}



