import { MusicType, SoundType } from "../Audio/AudioType";
import { CocosUtil } from "../../Hierachy/CocosUtil";
import GameManager from "../GameManager";
import { EventSystem, EventType } from "../EventSystem";


export class AudioManager {

    private static readonly MusicFolderUrl = "Audio/Music/";
    private static readonly SoundFolderUrl = "Audio/Sound/";

    private static m_MusicClipMap: Map<string, cc.AudioClip> = new Map();
    private static m_SoundClipMap: Map<string, cc.AudioClip> = new Map();

    private static m_TempPlayingSounds: Map<string, number> = new Map();
    private static m_TempPlayingMusics: Map<string, number> = new Map();

    static async init() {
        // cc.loader.load('http://rd07.zwai.com.tw/Resources/QueenSlot/Audio/Audio03.mp3', (err, asset) => {
        //     console.log(asset)
        // })
        return new Promise(async (res, rej) => {

            let assets = await Promise.all([
                CocosUtil.loadResDirSync(AudioManager.MusicFolderUrl),
                CocosUtil.loadResDirSync(AudioManager.SoundFolderUrl),
            ]);
            console.log(assets)
            this.m_MusicClipMap = this.clipsToMap(assets[0]);
            this.m_SoundClipMap = this.clipsToMap(assets[1]);
            res(
                ''
            );
        });
    }

    static playMusic(music: MusicType, loop: boolean = true): number {
        if (music.toString() == '')
            return;
        let clip = AudioManager.getClip(this.m_MusicClipMap, music);
        let id = cc.audioEngine.playMusic(clip, loop);
        this.m_TempPlayingMusics.set(music, id);
        return id;

    }

    static playSound(sound: SoundType, loop: boolean = false
        , newClip: boolean = false): number {
        if (sound.toString() == '')
            return;

        if (!newClip)
            this.stopSound(sound);
        let clip = AudioManager.getClip(this.m_SoundClipMap, sound)
        let id = cc.audioEngine.playEffect(clip, loop);
        //DebugUtil.log(id);
        this.m_TempPlayingSounds.set(sound, id);
        return id;
    }


    static stopMusic() {
        // var state = this.getAudioState(music);
        // if (state == cc.audioEngine.AudioState.PLAYING) {
        //     let id = this.m_TempPlayingSounds.get(music);
        cc.audioEngine.stopMusic();
        // }
    }
    static stopSound(sound: SoundType) {
        var state = this.getAudioState(sound);
        if (state == cc.audioEngine.AudioState.PLAYING) {
            let id = this.m_TempPlayingSounds.get(sound);
            cc.audioEngine.stopEffect(id);
        }
    }

    static getAudioState(type: MusicType | SoundType): cc.audioEngine.AudioState {
        let id = -1;

        if (CocosUtil.isEnum(type, MusicType))
            id = this.m_TempPlayingMusics.get(type);
        else if (CocosUtil.isEnum(type, SoundType))
            id = this.m_TempPlayingSounds.get(type);

        if (id == undefined || id < 0)
            return cc.audioEngine.AudioState.STOPPED;
        return cc.audioEngine.getState(id);
    }

    static setMusicVolume(volume: number) {
        cc.audioEngine.setMusicVolume(volume);
    }

    static setSoundVolume(volume: number) {
        cc.audioEngine.setEffectsVolume(volume);
    }

    private static getClip(map, type: MusicType | SoundType): cc.AudioClip {
        let result = map.get(type);

        if (result === undefined)
            cc.log("Cannot find sound of type: " + type);

        return result;
    }

    private static clipsToMap(clips: cc.AudioClip[]): Map<string, cc.AudioClip> {
        let map = new Map<string, cc.AudioClip>();
        clips.forEach(clip => {
            map.set(clip.name, clip);
        });

        return map;
    }
}
