import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";
import CommonUI_ViewController from "./CommonUI_ViewController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Wheel_ViewController extends CommonUI_ViewController {



    PayLine_Group: cc.Node = null;

    protected start(): void {
        this.PayLine_Group = GameManager.GM.Instance_WheelManager.node.getChildByName('PayLine_Lines')
        this.ingoreNodeName =
            [
                'Symbol_0',
                'Symbol_1',
                'Symbol_2',
                'Symbol_3',
                'Symbol_4',
                'Symbol_5',
                "effect_axis1",
                "effect_axis2",
                "effect_axis3",
                "effect_axis4",
                "effect_axis5",
                "PayLine_Lines"
            ]
        EventSystem.on(EventType.ScreenChange, () => {
            this.commonUI_PanleLayoutSetting();
        })
    }




    other_commonUI_PanleLayoutSetting = () => {


        if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
            this.PayLine_Group.setScale(1)
            this.PayLine_Group.setPosition(0, 0)
            //     // this.Rollers.forEach(_roller => {
            //     //     _roller.Symbol_migration_distance = 125
            //     // })
            //     // this.WheelAreaMask.setPosition(-24.909, -66.3)
            //     // this.WheelAreaMask.setContentSize(790, 410)
            //     // this.WheelAreaMask.anchorY = 0.5

        }
        else {
            this.PayLine_Group.setScale(0.7)
            this.PayLine_Group.setPosition(0, 235 - 410)
            //     // this.Rollers.forEach(_roller => {
            //     //     _roller.Symbol_migration_distance = 125
            //     // })
            //     // this.WheelAreaMask.anchorY = 0.7
            //     // this.WheelAreaMask.setContentSize(750, 383.1)
            //     // this.WheelAreaMask.setPosition(0, 0)

        }
    }

}
