import { EventSystem, EventType } from "./EventSystem";
import { GameScreenLayout } from "./GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DebugTool extends cc.Component {


    @property([cc.SpriteFrame])
    Bound: cc.SpriteFrame[] = [];

    BG: cc.Sprite = null;
    start() {
        EventSystem.on(EventType.ScreenChange, (layout) => { this.ChangeLayoutBG(layout) })
        this.BG = this.node.getComponent(cc.Sprite)
    }

    ChangeLayoutBG(layout) {

        if (layout == GameScreenLayout.Horizontal) {
            this.BG.spriteFrame = this.Bound[0]
        }
        else {
            this.BG.spriteFrame = this.Bound[1]
        }
    }


}
