import CommonUI_ViewController from "./CommonUI/CommonUI_ViewController";
import { EventSystem, EventType } from "./EventSystem";
import GameManager, { GameScreenLayout } from "./GameManager";
import RemoteAssetManager from "./RemoteAssetManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class BonusCutSceneIn_ViewController extends CommonUI_ViewController {

    @property(cc.Node)
    BonusCutScene: cc.Node = null;

    @property(cc.SpriteFrame)
    BonusCutSceneImg: cc.SpriteFrame[] = []

    @property(cc.Sprite)
    tip: cc.Sprite = null;

    start() {
        EventSystem.on(EventType.AssetLoaded, () => {
            // this.BonusCutSceneImg[0] = RemoteAssetManager.self.getSpriteFrame('inbonusgameword_L')
            // this.BonusCutSceneImg[1] = RemoteAssetManager.self.getSpriteFrame('inbonusgametxt_p')
            if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
                this.tip.spriteFrame = this.BonusCutSceneImg[0]
            }
            else {
                this.tip.spriteFrame = this.BonusCutSceneImg[1]
            }
        })

        EventSystem.on(EventType.ScreenChange, () => {
            if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
                this.tip.spriteFrame = this.BonusCutSceneImg[0]
            }
            else {
                this.tip.spriteFrame = this.BonusCutSceneImg[1]
            }
        })
    }

    other_commonUI_PanleLayoutSetting = () => { }




}
