import { EventSystem, EventType } from "../EventSystem";
import GameServer from "../FakeServer/GameServer";
import GameManager, { GameScreenLayout } from "../GameManager";
import MysteryData from "./MysteryData";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Mystery extends cc.Component {


    @property(cc.Node)
    InfoPage: cc.Node = null;

    @property(cc.Button)
    Icon: cc.Button = null;

    @property(cc.Node)
    MysteryBonusPage: cc.Node = null;

    @property(cc.Label)
    tip1: cc.Label = null;
    @property(cc.Label)
    tip2: cc.Label = null;
    @property(cc.Label)
    tip3: cc.Label = null;

    @property([cc.SpriteFrame])
    IconSprs: cc.SpriteFrame[] = [];


    private _InfoContent: string = '';
    public get InfoContent(): string {
        return this._InfoContent;
    }
    public set InfoContent(v: string) {

        this._InfoContent = v;
        if (v != '' && v != null && v != undefined) {
            cc.loader.load(v, (err, res) => {
                if (err)
                    throw err
                else {
                    this.InfoPage.getChildByName('txt_Info').getComponent(cc.Sprite).spriteFrame
                        = new cc.SpriteFrame(res)
                }
            })
        }
    }


    private _hasBonus: boolean = false;
    public get hasBonus(): boolean {
        return this._hasBonus;
    }
    public set hasBonus(v: boolean) {
        this._hasBonus = v;
        this.Icon.node.parent.active = v;
    }

    mData: MysteryData[] = [];

    screenChange() {
        if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
            this.Icon.node.setPosition(-600, 305, 0)
            this.InfoPage.getChildByName('txt_Info').scale = 1;
        }
        else {
            this.Icon.node.setPosition(305, 600, 0)
            this.InfoPage.getChildByName('txt_Info').scale = 0.75;
        }

        if (GameManager.GM.ServerPortocolMgr.language == 'zh-CHS')
            this.Icon.node.getComponentInChildren(cc.Sprite).spriteFrame = this.IconSprs[0]
        else {
            this.Icon.node.getComponentInChildren(cc.Sprite).spriteFrame = this.IconSprs[1]
        }
    }

    onLoad() {

        this.screenChange()
        EventSystem.on(EventType.ScreenChange, () => { this.screenChange() })

        this.Icon.node.on('click', () => {
            this.InfoPage.active = true;
        })

        this.InfoPage.getChildByName('btn_close').getComponent(cc.Button).node.on('click', () => {
            this.InfoPage.active = false;
        })
    }

    showMysteryBonus(mystery: MysteryData) {
        this.MysteryBonusPage.active = true;
        return new Promise((res, rej) => {

            this.MysteryBonusPage.getChildByName('bonus').getComponent(cc.Label).string = `${GameManager.GM.userUI.currency} ${mystery.amount.toLocaleString()}`
            GameManager.GM.SetBalance(GameServer.self.UserCredit + mystery.amount)
            this.scheduleOnce(() => {
                this.MysteryBonusPage.active = false;
                res('')
            }, 3)
        })

    }
}
