import { CocosUtil } from "./CocosUtil";

declare global {
    interface Array<T> {
        /** * Remove a element from array*/
        remove(item: T): void;
        /** * Get a random element from array*/
        random(): T;
        /** * Get a random index from array*/
        randomIndex(): number;
        /** * Is the element in the array?*/
        has(item: T): boolean;
        /** * Get a copy of last element from array*/
        last(): T;
    }

    interface Number {
        toPrecisionf(precision: number): number;
        toPrecisionStr(precision?: number): string;
    }

    interface String {
        toInt(): number;
        toNumber(): number;
    }

    interface Node {
        removeAllChild(): void;
    }

    namespace cc {
        interface Vec2 {
            /** * Get a number between x and y*/
            random(): number;
            /** * Get a intenger between x and y*/
            randomInt(): number;
        }
    }
}

Array.prototype.remove = function (item) {
    let id = this.indexOf(item);
    id !== -1 && this.splice(id, 1);
}

Array.prototype.last = function () {
    return this[this.length - 1];
}

Array.prototype.has = function (item) {
    let id = this.indexOf(item);
    return id !== -1;
}

Array.prototype.random = function () {
    return this[this.randomIndex()];
}

Array.prototype.randomIndex = function () {
    return CocosUtil.randomRangeInt(0, this.length - 1);
}

Number.prototype.toPrecisionf = function (precision: number) {
    let p = Math.pow(10, precision);
    return Math.round(this * p) / p;
}

Number.prototype.toPrecisionStr = function (precision: number = 2) {
    return this.toLocaleString('en-US', { minimumFractionDigits: precision, maximumFractionDigits: precision });
}

String.prototype.toInt = function () {
    let str = this.replace(/,/g, '');
    return Number.parseInt(str);
}

String.prototype.toNumber = function () {
    let str = this.replace(/,/g, '');
    return Number.parseFloat(str);
}

Node.prototype.removeAllChild = function () {
    while (this.childElementCount > 0) {
        this.removeChild(this.firstChild);
    }
}

cc.Vec2.prototype.random = function () {
    return CocosUtil.randomRange(this.x, this.y);
}

cc.Vec2.prototype.randomInt = function () {
    return CocosUtil.randomRangeInt(this.x, this.y);
}

//
export { }
