
import { AudioManager } from "./Audio/AudioManager";
import { SoundType } from "./Audio/AudioType";
import { EventSystem, EventType } from "./EventSystem";
import GameServer from "./FakeServer/GameServer";
import GameManager from "./GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class BoosterController extends cc.Component {

    @property(cc.Button)
    rowTest: cc.Button = null;

    @property([cc.SpriteFrame])
    TopRow_spr: cc.SpriteFrame[] = []

    @property(cc.Float)
    Symbol_column_count: number = 6;//Symbol

    @property(cc.Float)
    Symbol_Size_width: number = 145; //

    @property(cc.Float)
    Symbol_Speed: number = 0.1;//A~B

    BoosterData: number[] = [7, 2, 3, 10, 6, 5, 8, 1, 9, 4, 0]

    RowData: number[] = [0, 7, 2, 3, 10]//

    Row_Symbols: cc.Node[] = []; //Node

    AnimMap: Map<number, string> = new Map()

    // popBoosterData() {
    //     // let temp = this.BoosterData.shift()
    //     // this.BoosterData.push(temp)
    //     // for (let i = 0; i < this.RowData.length; i++) {
    //     //     this.RowData[i] = this.BoosterData[i]
    //     // }
    //     // let temp = this.BoosterData.shift()
    //     // this.BoosterData.push(temp)
    //     for (let i = 0; i < this.RowData.length; i++) {
    //         // this.RowData = GameManager.GM.ServerResult.MainResultData.TopRow
    //     }
    // }

    init() {
        this.AnimMap.set(0, 'freespinx5')//0
        this.AnimMap.set(1, 'freespinx8')//1
        this.AnimMap.set(2, 'freespinx12')//2
        this.AnimMap.set(3, 'winx2')//3
        this.AnimMap.set(4, 'winx5')//4
        this.AnimMap.set(5, 'winx10')//5
        this.AnimMap.set(6, 'betx5')//6
        this.AnimMap.set(7, 'betx25')//7
        this.AnimMap.set(8, 'betx250')//8
        this.AnimMap.set(9, 'betx500')//9
        this.AnimMap.set(10, 'ex')//10

        this.node.children.forEach(child => {
            this.Row_Symbols.push(child)
        });

        EventSystem.emit(this.node, EventType.BigWininit)
        this.initTopRow();//initRow //  Boosterprefab 

        let AnimName = []
        this.RowData.forEach(data => {
            AnimName.push(this.AnimMap.get(data))
        });

    }


    putLastData() {
        let arr = []

        if ((this.RowData[this.RowData.length - 1]) == this.BoosterData[this.BoosterData.length - 1]) {
            this.Row_Symbols[5].children[1].getComponent(cc.Sprite).spriteFrame = this.TopRow_spr[this.BoosterData[0]]

            this.RowData.shift()
            this.RowData.push(this.BoosterData[0])

            //debug--------
            // this.RowData.forEach(element => {
            //     arr.push(this.AnimMap.get(element))
            // })
            // console.log(arr)
        }
        else {

            this.Row_Symbols[5].children[1].getComponent(cc.Sprite).spriteFrame = this.TopRow_spr[this.RowData[this.RowData.length - 1]]
            this.RowData.shift()

            let CompareLastElement = this.BoosterData.indexOf(this.RowData[this.RowData.length - 1])
            this.RowData.push(this.BoosterData[CompareLastElement + 1])
            //debug
            // this.RowData.forEach(element => {
            //     arr.push(this.AnimMap.get(element))
            // })
            // console.log(arr)

        }
        // console.log('BB', this.RowData)
    }

    onSlideBoost(targetNode: cc.Node, speed: number) {
        this.putLastData()
        this.scheduleOnce(async () => {
            targetNode.children.forEach(childNode => {
                childNode.children[0].anchorY = 0.5
                childNode.children[0].getComponent(cc.Animation).play('Ghost_move')
            })
            await this.moveleft(targetNode, speed)
        }, 0.5)

    }

    moveleft(targetNode: cc.Node, speed: number) {

        return new Promise((resolve, reject) => {
            cc.tween(targetNode)
                .by(speed, { position: cc.v3(-880 / 6, 0, 0) })//NodeSymbole
                .by(0, { position: cc.v3(880 / 6, 0, 0) })
                .call(() => { this.ChangeRow(); })
                .call(resolve)
                .start()
            if (targetNode == null) reject(cc.log(''))
        }).then(() => {

            targetNode.children.forEach(childNode => {
                childNode.children[0].getComponent(cc.Animation).play('Ghost_burn')
                cc.tween(childNode.children[0]).to(0.3, { opacity: 0 }).start()
            })


            this.showTopRow();
            GameManager.GM.isBoosterStoped = true
            EventSystem.emit(this.node, EventType.Stoped)

        });
    }

    initTopRow() {
        this.RowData.forEach((num, idx) => {
            this.Row_Symbols[idx].children[1].getComponent(cc.Sprite).spriteFrame = this.TopRow_spr[num]
        })
    }

    ChangeRow = (ServerData: number = -1) => {

        // let BoosterPrefabAmount = this.boost_Prefabs.length - 1//Booster;
        // let getRandonBooster = Math.round(Math.random() * 10);//  //0~10
        // ServerData = GameManager.GM.Instance_DebugManager.OverWritedata();
        // cc.log('ServerData=', ServerData)

        if (!this.node.getComponent(cc.Layout).enabled)
            this.node.getComponent(cc.Layout).enabled = true

        // this.popBoosterData()
        if (ServerData != -1) {
            this.RowData[4] = ServerData
        }

    }

    CheckRow(idxs: number[], targetNumber: number) {
        let checkresult = [];

        idxs.forEach(idx => {
            if (idx == targetNumber)
                checkresult.push(idx)
        })
        return checkresult;
    }


    showTopRow() {
        // AudioManager.playSound(SoundType.LANTER_SHOWTEXT)
        let animName = [];
        this.RowData.forEach((num, idx) => {
            animName[idx] = this.AnimMap.get(num);
        })

        if (GameManager.GM.hasResult) {
            this.RowData = GameServer.self.ServerData.MainResultData.TopRow
        }

        for (let idx = 0; idx < animName.length; idx++) {
            this.Row_Symbols[idx].children[1].getComponent(cc.Sprite).spriteFrame = this.TopRow_spr[this.RowData[idx]]
        }

    }

    TopRow_win(_ghostEffectSwitch: boolean[]) {
        // this.TopRow_Reset()
        for (let i = 0; i < _ghostEffectSwitch.length; i++) {
            if (_ghostEffectSwitch[i]) {
                // AudioManager.playSound(SoundType.TOPROW_BINGO)
                this.node.children[i].children[0].opacity = 255
                this.node.children[i].children[0].anchorY = 0.4
                this.node.children[i].children[0].getComponent(cc.Animation).play('Ghost_Redburn')

                let animName = this.AnimMap.get(this.RowData[i])
                this.node.children[i].children[1].getComponent(cc.Animation).play(animName)

            }
        }

    }

    TopRow_Reset() {
        for (let idx = 0; idx < 5; idx++) {

            this.node.children[idx].children[0].getComponent(cc.Animation).play('Ghost_burn')
            this.node.children[idx].children[0].scale = 1;
            this.node.children[idx].children[0].anchorY = 0.5;
            this.node.children[idx].children[1].getComponent(cc.Animation).stop();
        }
        GameManager.GM.Instance_WheelManager.TopRowQueen = [false, false, false, false, false]

    }

}





