import { AudioManager } from "./Audio/AudioManager";
import { SoundType } from "./Audio/AudioType";
import { EventSystem, EventType } from "./EventSystem";
import GameServer from "./FakeServer/GameServer";
import GameManager, { GameScreenLayout } from "./GameManager";
import RemoteAssetManager from "./RemoteAssetManager";
import UI_interface from "./UI/UI_interface";


const { ccclass, property } = cc._decorator;

export enum BonusRollerName {
    axis1 = "axis1",
    axis2 = "axis2",
    axis3 = "axis3",
    axis4 = "axis4",
    axis5 = "axis5",
}

@ccclass
export default class BonusRoller extends cc.Component {


    @property(cc.Float)
    Symbol_column_count: number = 3;//Symbol

    @property(cc.Float)
    Symbol_migration_distance: number = 200; //

    Column_Symbols: cc.Node[] = [];
    ColumnData: number[] = []; // 

    m_Sprite: cc.SpriteFrame[] = [];
    m_stoped: boolean = false; //

    Spine_symbols: sp.SkeletonData[] = []
    bubbleImgScale: number = 0.3;

    @property([cc.SpriteFrame])
    bubbleDigital: cc.SpriteFrame[] = []
    bubbleNum: number = 0;
    myName = ''

    protected onLoad(): void {
        this.node.children.forEach((childNode) => {
            this.Column_Symbols.push(childNode);
        });
        EventSystem.on(EventType.AssetLoaded, () => {
            this.init();
            this.bubbleDigital = this.bubbleDigitalInit()
            console.log('BonusRollerBonusRollerBonusRoller')
        })

    }

    bubbleDigitalInit(): cc.SpriteFrame[] {
        let DigitalImgs = []
        for (let idx = 0; idx <= 11; idx++) {
            let texture: cc.Texture2D = RemoteAssetManager.self.getAsset(`bubbleDigital_${idx}`)
            DigitalImgs[idx] = new cc.SpriteFrame(texture)
        }
        // console.log(DigitalImgs)
        return DigitalImgs;
    }

    init() {
        this.myName = this.node.name;
        //Node 
        this.Symbol_column_count = this.node.childrenCount;


        for (let i = 0; i <= 12; i++) {
            //Roller Symbol
            let texture2D = RemoteAssetManager.self.getAsset(`symbol_${i}`)
            let img: cc.SpriteFrame = new cc.SpriteFrame(texture2D)
            img.name = `symbol_${i}`
            this.m_Sprite.push(img)
        }

        this.ColumnData = this.firstGameResult(); //

        // console.log(`${this.node.name}=>\n${this.ColumnData}`)

        //
        //SymboleNode  
        GameManager.GM.count_Rollerinit += 1;
    }


    Column_bubbleData = [];
    //
    private firstGameResult(): number[] {
        let RandonIndexArray: number[] = [];
        for (let i = 0; i < this.Symbol_column_count; i++) {
            RandonIndexArray[i] = this.Random(11, 1)
            this.Column_Symbols[i].getComponent(cc.Sprite).spriteFrame = this.m_Sprite[RandonIndexArray[i]];
            this.Column_bubbleData[i] = this.bubbleRandom().toString();
        }
        return RandonIndexArray;//
    }

    Random(max, min) {
        return Math.round(Math.random() * (max - min) + min);
    }

    private bubbleRandom() {
        let num = 1;
        num = (Math.round(this.Random(2000, 5))) / 10
        num *= GameServer.self.getTotalBet()
        return num
    }
    private font_Transfer_to_texture(num) {
        let valueStr = GameManager.toStringThousandth(num)
        this.updateNumImgs(valueStr);
        // this.updateNumImgs(num);
    }

    private updateNumImgs(valueStr: string) {
        let roll = this.node
        let balls = roll
        let layout: cc.Layout = null;

        balls.children.forEach((ball) => {

            if (ball.getComponentInChildren(cc.Layout) == null) {
                ball.children[0].addComponent(cc.Layout)
            }

            layout = ball.children[0].getComponent(cc.Layout)
            layout.spacingX = -10
            layout.type = cc.Layout.Type.HORIZONTAL
            layout.resizeMode = cc.Layout.ResizeMode.CONTAINER
            layout.affectedByScale = true;
            layout.node.anchorY = 1
            layout.node.y = (-100 * this.bubbleImgScale) - 5
            layout.node.scale = this.bubbleImgScale

            if (layout.node != null) {
                layout.node.destroyAllChildren();
                for (const char of valueStr) {
                    let charNode = new cc.Node();
                    charNode.setParent(layout.node);
                    // charNode.scale = this.bubbleImgScale;
                    let spr = charNode.addComponent(cc.Sprite);
                    let tex = this.getSpriteByChar(char);
                    if (tex) {
                        spr.spriteFrame = tex;
                        spr.sizeMode = cc.Sprite.SizeMode.TRIMMED
                        spr.trim = true;
                        spr.node.anchorY = 0
                    }

                    // spr.node.parent.y = -((spr.node.parent.parent.height) * 0.25)
                }
            }
        })



    }


    private getSpriteByChar(char: string): cc.SpriteFrame {

        if (char.match(/[0-9]/)) {
            let num = Number.parseInt(char);
            return this.determineSpr(num)
        }
        else if (char === ',')
            return this.determineSpr(10);
        else if (char === '.')
            return this.determineSpr(11);

        return null;
    }


    private determineSpr(num: number) {
        return this.bubbleDigital[num]
    }

}
