import { EventType } from "./EventSystem";
import GameManager from "./GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class AchievementsUI extends cc.Component {


    @property(cc.Node)
    EnergyBar_UIRoot: cc.Node = null;

    @property(cc.Node)
    MG_Achievements: cc.Node = null;

    @property(cc.Button)
    FG_Achievement: cc.Button = null;

    @property(cc.Node)
    CG_Page: cc.Node = null;

    lbl_Tip: cc.Label = null;

    Event1_Cg: sp.Skeleton = null;
    Event2_Cg: sp.Skeleton = null;

    TweenEvent1: cc.Tween = null;
    TweenEvent2: cc.Tween = null;



    isInit: boolean = false;

    public isEvent2_conditionComplete: boolean = false
    public isEvent2_opend: boolean = false

    public isEvent1_conditionComplete: boolean = false;
    public isEvent1_opend: boolean = false;

    public readonly MaxWidth = 570;
    public MaxAccumulation_EnergyBar = 100;
    public EnergyBarUI: cc.Node = null;

    private _currentAccumulation_Energy: number = 0;
    public get currentAccumulation_Energy(): number {
        return this._currentAccumulation_Energy;
    }
    public set currentAccumulation_Energy(v: number) {
        this._currentAccumulation_Energy = v;
        let TargetWidth = Math.round((this.MaxWidth / this.MaxAccumulation_EnergyBar) * this._currentAccumulation_Energy)
        if (TargetWidth >= this.MaxWidth)
            TargetWidth = this.MaxWidth
        this.FillEnergyBar(TargetWidth)

    }




    public readonly MaxScale = 1
    public MaxAccumulation_HeartShape = 10;
    public HeartShape: cc.Node = null;
    public HeartShapeTexture: cc.Sprite = null;
    private _currentHeartPercent: number = 0;
    public get currentHeartPercent(): number {
        return this._currentHeartPercent;
    }
    public set currentHeartPercent(v: number) {
        this._currentHeartPercent = v;
        let scale = (this.MaxScale / this.MaxAccumulation_HeartShape) * this._currentHeartPercent
        this.HeartShape.scale = scale > 1.0 ? 1.0 : scale
    }

    public prev: cc.Button = null;
    public next: cc.Button = null;
    public close: cc.Button = null;
    currentIndex: number = 0;

    start() {
        this.lbl_Tip = this.CG_Page.getChildByName('next').getComponentInChildren(cc.Label)
        this.lbl_Tip.string = "NEXT"
        //Event1
        this.EnergyBarUI = this.EnergyBar_UIRoot.getChildByName('accumulation')
        this.currentAccumulation_Energy = 0;

        //Event2
        this.HeartShape = this.MG_Achievements.getChildByName("FillMask")
        this.HeartShapeTexture = this.HeartShape.getChildByName('texture').getComponent(cc.Sprite)
        this.HeartShapeTexture.node.color = cc.color(155, 155, 155)

        this.currentHeartPercent = 0;
        //playArea
        this.prev = this.CG_Page.getChildByName('prev').getComponent(cc.Button)
        this.next = this.CG_Page.getChildByName('next').getComponent(cc.Button)
        this.close = this.CG_Page.getChildByName('close').getComponent(cc.Button)

        //init
        this.Event1_Cg = this.CG_Page.getChildByName('Event1CG').getComponent(sp.Skeleton)
        this.Event2_Cg = this.CG_Page.getChildByName('Event2CG').getComponent(sp.Skeleton)

        this.MG_Achievements.on('click', () => {
            if (this.currentHeartPercent >= this.MaxAccumulation_HeartShape) {
                GameManager.GM.userUI.btn_Stop.node.emit('click')
                if (GameManager.GM.GameState != EventType.Normal) return;
                if (this.TweenEvent2 != null) {
                    this.TweenEvent2.stop();
                    this.MG_Achievements.getChildByName('FillMask').getChildByName('texture').color = cc.color(255, 255, 255)
                }
                GameManager.GM.AchievementsUI.CG_Page.active = true;
                GameManager.GM.AchievementsUI.Event2_Cg.node.active = true;
                GameManager.GM.AchievementsUI.isEvent2_opend = true;
                let ActionName = Object.values(Event2AnimationName)[this.currentIndex]
                this.Event2_Cg.setAnimation(0, ActionName, true)
                this.lbl_Tip.node.active = true;
            }
        })

        this.FG_Achievement.node.on('click', () => {
            GameManager.GM.userUI.btn_Stop.node.emit('click')
            if (GameManager.GM.GameState != EventType.Normal) return;
            if (this.TweenEvent1 != null) this.TweenEvent1.stop();
            this.FG_Achievement.node.active = true;
            // if (GameManager.GM.AchievementsUI.Event2open == false) {
            GameManager.GM.AchievementsUI.isEvent1_opend = true;
            GameManager.GM.AchievementsUI.isEvent1_conditionComplete = true;
            GameManager.GM.AchievementsUI.CG_Page.active = true;
            GameManager.GM.AchievementsUI.Event1_Cg.node.active = true;
            this.FG_Achievement.node.getChildByName('FillMask').getChildByName('texture').color = cc.color(255, 255, 255)
            this.lbl_Tip.node.active = true;
            let ActionName = Object.values(Event1AnimationName)[this.currentIndex]
            this.Event1_Cg.setAnimation(0, ActionName, true)
            // }
        })

        this.next.node.on('click', () => {


            let MaxLength = 0;
            if (this.Event2_Cg.node.activeInHierarchy) {
                this.Event1_Cg.node.active = false;
                MaxLength = Object.keys(Event1AnimationName).length
            }
            if (this.Event1_Cg.node.activeInHierarchy) {
                this.Event2_Cg.node.active = false;
                MaxLength = Object.keys(Event2AnimationName).length
            }

            if (this.currentIndex + 1 >= MaxLength)
                return;
            else
                this.currentIndex += 1;

            if (this.Event2_Cg.node.activeInHierarchy) {
                let ActionName = Object.values(Event1AnimationName)[this.currentIndex]
                this.Event2_Cg.setAnimation(0, ActionName, true)
            }
            if (this.Event1_Cg.node.activeInHierarchy) {
                let ActionName = Object.values(Event2AnimationName)[this.currentIndex]
                this.Event1_Cg.setAnimation(0, ActionName, true)
            }

        })

        this.close.node.on('click', () => {

            this.currentIndex = 0;
            this.Event2_Cg.node.active = false;
            this.Event1_Cg.node.active = false;
            // this.AchievementPlayArea_Root.getChildByName('BlackBackground').active = false;
            this.CG_Page.active = false;
            this.lbl_Tip.node.active = false;
        })


    }

    DisplayEnergyUI(bool: boolean) {
        this.EnergyBar_UIRoot.opacity = bool ? 255 : 0

    }

    FillEnergyBar(TargetWidth) {
        cc.tween(this.EnergyBarUI)
            .to(1, { width: TargetWidth })
            .start()

    }

    onEnable() {
        if (this.isInit) {
            this.Event2_Cg.setAnimation(0, Object.values(Event1AnimationName)[0], true)
            this.Event1_Cg.setAnimation(0, Object.values(Event2AnimationName)[0], true)
        }
        else {
            this.isInit = true;
        }



    }

    checkEvent1Trigger() {
        if (GameManager.GM.AchievementsUI.currentAccumulation_Energy >= GameManager.GM.AchievementsUI.MaxAccumulation_EnergyBar) {
            this.FG_Achievement.node.active = true;

            if (GameManager.GM.AchievementsUI.isEvent1_conditionComplete) return;//trigger
            this.TweenEvent1 = cc.tween(this.FG_Achievement.node.getChildByName('FillMask').getChildByName('texture')).repeatForever(
                cc.tween(this.HeartShapeTexture.node)
                    .to(.3, { color: cc.color(200, 200, 200) })
                    .to(.3, { color: cc.color(255, 255, 255) })
                    .start()
            ).start();

            GameManager.GM.AchievementsUI.isEvent1_conditionComplete = true;

        }
    }

    checkEvent2Trigger() {


        if (GameManager.GM.AchievementsUI.currentHeartPercent >= GameManager.GM.AchievementsUI.MaxAccumulation_HeartShape) {
            if (GameManager.GM.AchievementsUI.isEvent2_conditionComplete) return;//trigger
            this.TweenEvent2 = cc.tween(this.HeartShapeTexture.node).repeatForever(
                cc.tween(this.HeartShapeTexture.node)
                    .to(.3, { color: cc.color(200, 200, 200) })
                    .to(.3, { color: cc.color(255, 255, 255) })
                    .start()
            ).start();
            GameManager.GM.AchievementsUI.isEvent2_conditionComplete = true;
        }
    }


}
export enum Event1AnimationName {
    phase1 = 'H02',
    phase2 = 'H03',
    phase3 = 'H04',
    phase4 = 'H07',
    phase5 = 'H08',
    phase6 = 'H09',

}
export enum Event2AnimationName {
    stay1 = 'H02',
    foreplay1 = 'H03',
    foreplay2 = 'H04',
    doggy1 = 'H05',
    doggy2 = 'H06',
    doggy3 = 'H07',
    doggy4 = 'H08',//transtion
    doggy5 = 'H09',//final

}

