import GameManager from "./GameManager";


const { ccclass, property } = cc._decorator;

@ccclass
export default class CountDownClock extends cc.Component {


    Timeout = -1;
    TimeStart = 10;


    private _cTime: number = 10;
    public get cTime(): number {
        return this._cTime;
    }
    public set cTime(v: number) {
        if (v < this.Timeout && v > this.TimeStart) return

        this._cTime = v;
        this.DisplayTime.string = this._cTime.toString();

    }

    _temp = 0;

    // @property(cc.Label)
    DisplayTime: cc.Label = null;
    // onLoad () {}

    start() {


    }

    update(dt) {
        this._temp += dt;
        if (this._temp > 1) {
            this._temp = 0;
            this.cTime -= 1;
        }
        if (this.cTime == this.Timeout) {
            GameManager.GM.EnterFreeGame.emit('click')
            this.node.opacity = 0;
            this.node.active = false;
        }
    }


    protected onEnable(): void {

        if (this.DisplayTime == null)
            this.DisplayTime = this.node.getComponent(cc.Label);
        this.cTime = this.TimeStart
    }
}
