import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";


const { ccclass, property } = cc._decorator;

@ccclass
export default class TopUI_ViewController extends cc.Component {

    @property(cc.Node)
    TopUI: cc.Node = null;
    @property(cc.Node)
    TopUI_hori: cc.Node = null;
    @property(cc.Node)
    TopUI_Verti: cc.Node = null;

    start() {


        EventSystem.on(EventType.ScreenChange, () => {
            if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
                this.TopUI.setPosition(this.TopUI_hori.getPosition(cc.v3()))
                this.TopUI.setScale(this.TopUI_hori.getScale(cc.v3()))
                this.TopUI.getChildByName('TopSpr').scale = 1
                this.TopUI.getChildByName('TopSpr').setContentSize(1000, 80)
                this.TopUI.children[0].setPosition(this.TopUI_hori.children[0].getPosition())
                this.TopUI.children[1].setPosition(this.TopUI_hori.children[1].getPosition())
                this.TopUI.children[2].setPosition(this.TopUI_hori.children[2].getPosition())
                this.TopUI.children[3].setPosition(this.TopUI_hori.children[3].getPosition())

            }
            else {
                this.TopUI.setPosition(this.TopUI_Verti.getPosition(cc.v3()))
                this.TopUI.setScale(this.TopUI_Verti.getScale(cc.v3()))
                this.TopUI.getChildByName('TopSpr').scale = 1.3333
                this.TopUI.getChildByName('TopSpr').setContentSize(750, 96)
                this.TopUI.children[0].setPosition(this.TopUI_Verti.children[0].getPosition())
                this.TopUI.children[1].setPosition(this.TopUI_Verti.children[1].getPosition())
                this.TopUI.children[2].setPosition(this.TopUI_Verti.children[2].getPosition())
                this.TopUI.children[3].setPosition(this.TopUI_Verti.children[3].getPosition())
            }
        })
    }



    // update (dt) {}
}
