
const { ccclass, property } = cc._decorator;

@ccclass
export default class payline_reset extends cc.Component {


    LineAnimIndex: string = ''
    SpineComp: sp.Skeleton = null;

    start() {
        this.SpineComp = this.node.getComponent(sp.Skeleton)
    }


    protected onEnable(): void {
        if (this.SpineComp != null)
            this.SpineComp.setAnimation(0, this.LineAnimIndex, true)
    }

    protected onDisable(): void {
        if (this.SpineComp != null) {
            this.SpineComp.clearTracks();
            this.SpineComp.setToSetupPose();

        }
    }

    // update (dt) {}
}
