import { AudioManager } from "./Audio/AudioManager";
import { SoundType } from "./Audio/AudioType";
import { EventType } from "./EventSystem";
import GameServer from "./FakeServer/GameServer";
import GameManager, { BackgroundSpineEnum } from "./GameManager";
import RewardData from "./UI/RewardData";


const { ccclass, property } = cc._decorator;

@ccclass
export default class RewardMarqueen extends cc.Component {


    congratulateTips: cc.Label = null;

    // Award_info: {
    //     pay_symbol: cc.Sprite;
    //     pay: cc.RichText;
    // }

    pay_symbol: cc.Sprite;
    pay: cc.RichText;

    // onLoad () {}

    init() {
        this.congratulateTips = cc.find("Canvas/Mask/UI_interface/congratulateTips/New Label").getComponent(cc.Label)
        this.pay_symbol = cc.find('Canvas/Mask/UI_interface/top_infobar/Award_info/pay_symbol').getComponent(cc.Sprite)
        this.pay = cc.find('Canvas/Mask/UI_interface/top_infobar/Award_info/pay').getComponent(cc.RichText)

    }

    showSingleLine(Datas: RewardData) {
        GameManager.GM.EnabledTotalWinUI(false)
        let needTurnOffSymbol
        if (GameManager.GM.saveGameResult.length > 0) { //
            GameManager.GM.Instance_WheelManager.PayLine_Lines_DisableAll()
            GameManager.GM.Instance_WheelManager.PayLine_Lines_ShowLine(Datas.whichLine)
            GameManager.GM.Instance_WheelManager.PayLineEffect(Datas, needTurnOffSymbol, GameManager.GM.Instance_WheelManager.ServerResultFormatTo4x5(Datas.whichLine), true)
            // console.log((Datas[Idx].LineAward))
            this.pay.node.parent.active = true;
            this.pay_symbol.spriteFrame = GameManager.GM.Instance_WheelManager.static_Symbol[Datas.Symbol_ID - 1]
            this.pay_symbol.node.setContentSize(40, 40)
            this.pay.string = `x ${Datas.howmany}  ${GameManager.GM.language_LINE} ${Datas.whichLine}  ${GameManager.GM.language_PAYS} ${GameManager.toStringThousandth(Datas.LineAward)}  `

        }
    }

    ShowAllPayLine(Datas: RewardData[]) {

        let lbl_freetimeleft = GameManager.GM.userUI.top_infobar.getChildByName('lbl_FreeSpinLeft').getComponent(cc.Label)
        lbl_freetimeleft.string = '';
        this.pay_symbol.spriteFrame = null;
        this.pay.string = ``

        GameManager.GM.congratulateTips.string = ''
        let needTurnOffSymbol: cc.Node[] = [];


        if (Datas.length != 0) {
            //
            GameManager.GM.Instance_WheelManager.PayLineDatas.forEach(Data => {
                Data.LineNodes.forEach(node => {
                    node.opacity = 100;
                })
            });

        }

        if (Datas.length == 0) {
            (cc.log(''))
            // this.CompareSymbol(GameManager.GM.saveGameResult) //
            return;
        }


        for (let i = 0; i < Datas.length; i++) {
            if (Datas[i].whichLine == 41 && GameManager.GM.isFreeGameState == false) {
                GameManager.GM.ScatterReward();
                continue
            }
        }


        for (let i = 0; i < Datas.length; i++) {
            GameManager.GM.Instance_WheelManager.PayLine_Lines_ShowLine(Datas[i].whichLine)
            let Result = GameManager.GM.Instance_WheelManager.ServerResultFormatTo4x5(Datas[i].whichLine)
            GameManager.GM.Instance_WheelManager.PayLineEffect(Datas[i], needTurnOffSymbol, Result);
            needTurnOffSymbol.forEach(node => { node.active = false })
        }


        if (GameManager.GM.isFreeGameState) {
            // let ActionNames = [BackgroundSpineEnum.Bonus_Idle, BackgroundSpineEnum.Bonus_Win]
            // GameManager.GM.BonusGameBG_InsertActions(ActionNames)
            // GameManager.GM.FreeGameBG.setAnimation(0, BackgroundSpineEnum.Bonus_Win, true)
            GameManager.GM.anim_WinScore(true);


            GameManager.GM.setRoundScore(GameServer.self.ServerResult[GameServer.self.ServerDataIndex].TotalWin)
            GameManager.GM.DelayEndTime = 2;
            GameManager.GM.DelayTimeTrigger = true;

        } else {//mainGame
            // let actionNames = [BackgroundSpineEnum.MG_Win, BackgroundSpineEnum.MG_Idle]
            // GameManager.GM.MainGameBG_InsertActions(actionNames)
            // GameManager.GM.MainGameBG.setAnimation(0, BackgroundSpineEnum.MG_Win, true)
            GameManager.GM.EnabledTotalWinUI(true)
            GameManager.GM.anim_WinScore(true);
            if (GameManager.GM.userUI.LoopTimes > 0)
                GameManager.GM.DelayEndTime = 2
            else {
                GameManager.GM.DelayEndTime = 0
            }
            GameManager.GM.DelayTimeTrigger = true;
        }



    }

    m_PaylineTrigger: boolean = false;
    m_changetrigger: boolean = false
    m_showindex: number = 0;
    m_timeCounter: number = 0;


    m_Enable_ShowAllPayLine: boolean = false;
    m_ShowAllPayLine_TimeCount: number = 0;
    m_ShowAllPayLine_EndTime: number = 2;
    m_ShowAllPayLine_TimeTrigger: boolean = false;//

    activeShowAllPayLine() { this.m_Enable_ShowAllPayLine = true }//<---
    startLoopSingleLine() {
        this.m_Enable_ShowAllPayLine = false;
        this.m_ShowAllPayLine_TimeTrigger = false;
        this.m_ShowAllPayLine_TimeCount = 0;
        this.SingleLineOpenCounter();
    }

    update(dt) {
        // if (GameManager.GM.GameState != EventType.Normal) return



        if (this.m_Enable_ShowAllPayLine) {// 
            console.log("AllLine")
            this.ShowAllPayLine(GameManager.GM.saveGameResult)
            this.m_Enable_ShowAllPayLine = false;
            this.m_ShowAllPayLine_TimeTrigger = true;

        }

        if (this.m_ShowAllPayLine_TimeTrigger) {// LoopSingleLine
            if (this.m_ShowAllPayLine_TimeCount > this.m_ShowAllPayLine_EndTime) {
                this.m_ShowAllPayLine_TimeTrigger = false;
                this.m_ShowAllPayLine_TimeCount = 0;
                this.startLoopSingleLine();

            } else {
                this.m_ShowAllPayLine_TimeCount += dt;

            }
        }



        if (this.m_changetrigger) {

            this.showSingleLine(GameManager.GM.saveGameResult[this.m_showindex % GameManager.GM.saveGameResult.length])
            this.m_changetrigger = false;
        }
        if (this.m_PaylineTrigger) { //triggerChange
            this.m_timeCounter += dt;
            if (this.m_timeCounter > 2) {
                this.m_timeCounter = 0
                this.m_showindex += 1;
                if (this.m_showindex > GameManager.GM.saveGameResult.length) {//
                    this.closecounter();
                    this.activeShowAllPayLine();
                    console.log("showSingleLine")
                    return;
                }
                this.m_changetrigger = true;
            }
        }
    }

    resetcounter() {
        this.m_timeCounter = 0
        this.m_showindex = 0
    }

    SingleLineOpenCounter() {
        this.resetcounter();
        this.m_PaylineTrigger = true;
        this.m_changetrigger = true
    }
    closecounter() {
        this.resetcounter();
        this.m_PaylineTrigger = false;
        this.m_changetrigger = false
    }
}
