import AchievementsUI from "../AchievementsUI";
import { AudioManager } from "../Audio/AudioManager";
import { SoundType } from "../Audio/AudioType";
import { EventSystem, EventType } from "../EventSystem";
import GameServer from "../FakeServer/GameServer";
import GameManager from "../GameManager";
import UI_interface from "./UI_interface";
const { ccclass, property } = cc._decorator;

@ccclass
export default class SystemSetting_TS extends cc.Component {

    @property(cc.Toggle)
    tgl_quickspin: cc.Toggle = null;

    @property(cc.Toggle)
    tgl_sound: cc.Toggle = null;

    @property(cc.Toggle)
    tgl_music: cc.Toggle = null;

    @property(cc.Label)
    value_totalbet: cc.Label = null;

    @property(cc.Button)
    btn_Add: cc.Button = null;
    @property(cc.Button)
    btn_Sub: cc.Button = null;
    @property(cc.Button)
    btn_close: cc.Button = null;
    @property(cc.Button)
    btn_History: cc.Button = null;

    @property(cc.Button)
    Achievement1: cc.Button = null;
    @property(cc.Button)
    Achievement2: cc.Button = null;

    @property(cc.Button)
    exitgame: cc.Button = null;


    init() {

        this.value_totalbet.string = GameManager.toStringThousandth(GameServer.self.getTotalBet());

        this.Achievement1.node.on('click', () => {
            GameManager.GM.AchievementsUI.CG_Page.active = true;
            GameManager.GM.AchievementsUI.Event2_Cg.node.active = true;
            this.btn_close.node.emit('click')
        })
        this.Achievement2.node.on('click', () => {
            GameManager.GM.AchievementsUI.CG_Page.active = true;
            GameManager.GM.AchievementsUI.Event1_Cg.node.active = true;
            this.btn_close.node.emit('click')

        })

        // this.btn_Add.node.on('click', () => {
        //     AudioManager.playSound(SoundType.CLICK_BUTTON)
        //     GameServer.self.addBet();
        //     this.value_totalbet.string = GameManager.toStringThousandth(GameServer.self.getTotalBet())
        //     EventSystem.emit(this.node, EventType.UpdateUI)
        // })
        // this.btn_Sub.node.on('click', () => {
        //     AudioManager.playSound(SoundType.CLICK_BUTTON)
        //     GameServer.self.subBet();
        //     this.value_totalbet.string = GameManager.toStringThousandth(GameServer.self.getTotalBet())
        //     EventSystem.emit(this.node, EventType.UpdateUI)
        // })

        //QuickSpin
        this.tgl_quickspin.node.on('toggle', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            UI_interface.self.QuickSpinFlag = !UI_interface.self.QuickSpinFlag
            UI_interface.self.AutoSetting.tgl_quickspin.isChecked = UI_interface.self.SystemSetting.tgl_quickspin.isChecked = UI_interface.self.QuickSpinFlag
            GameManager.GM.Instance_WheelManager.resetStopPara();
        })

        // 
        this.tgl_sound.node.on('toggle', () => {
            // cc.Sound.playKeySound(cc.Sound.publicKeySound)
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            this.tgl_sound.isChecked != this.tgl_sound.isChecked
            // AudioManager.setSoundVolume(0)
            if (this.tgl_sound.isChecked)
                AudioManager.setSoundVolume(1)
            else
                AudioManager.setSoundVolume(0)
            //TODO 
        })

        //  
        this.tgl_music.node.on('toggle', () => {
            // cc.Sound.playKeySound(cc.Sound.publicKeySound)
            this.tgl_music.isChecked != this.tgl_music.isChecked
            if (this.tgl_music.isChecked)
                AudioManager.setMusicVolume(1)
            else
                AudioManager.setMusicVolume(0)
            AudioManager.playSound(SoundType.CLICK_BUTTON)

            //TODO 

        })

        this.btn_close.node.on('click', () => {
            this.node.getChildByName('tgl_System').getComponent(cc.Toggle).isChecked = false;
            this.node.getChildByName('Page_System ()').active = false;
            AudioManager.playSound(SoundType.CLICK_BUTTON)
        })

        this.btn_History.node.on('click', () => {
            console.log('clickHistory')
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            window.open(`${GameManager.GM.ServerPortocolMgr.HistoryURL}`)

        })
        this.exitgame.node.on('click', () => {
            console.log('home')
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            window.location.href = GameManager.GM.ServerPortocolMgr.HomeURL
        })

    }

}
