import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";
import CommonUI_ViewController from "./CommonUI_ViewController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class UI_interface_ViewController extends CommonUI_ViewController {

    @property(cc.Sprite)
    LongBarBG: cc.Sprite = null; //

    @property(cc.Sprite)
    BottomInfoBar: cc.Sprite = null;

    @property(cc.Sprite)
    TopInfoBar: cc.Sprite = null;

    @property([cc.SpriteFrame])
    AutoSettingBG: cc.SpriteFrame[] = [];

    @property(cc.Sprite)
    AUTOPLAYSETTINGPAGE: cc.Sprite = null;

    @property(cc.Sprite)
    SYSTEM_SETTINGPAGE: cc.Sprite = null;

    @property(cc.Layout)
    Award_info: cc.Layout = null;

    @property(cc.Label)
    AUTOSTOPSETTINGS: cc.Label = null;

    @property(cc.Button)
    lastbtn: cc.Button = null;
    @property(cc.Button)
    nextbtn: cc.Button = null;

    @property([cc.Node])
    InfoPageView: cc.Node[] = [];



    protected start(): void {

        this.ingoreNodeName = ['content', 'Award_info', 'pageView', 'tBackground', 'PageView_hori', 'PageView_verti']
    }

    other_commonUI_PanleLayoutSetting = () => {
        if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
            this.LongBarBG.enabled = true;
            this.BottomInfoBar.enabled = false;
            this.TopInfoBar.enabled = false;

            this.AUTOPLAYSETTINGPAGE.spriteFrame = this.AutoSettingBG[0];
            this.SYSTEM_SETTINGPAGE.spriteFrame = this.AutoSettingBG[0];
            // let description = cc.find('Titles/QUICK SPIN/New Label', this.SYSTEM_SETTINGPAGE.node)
            // description.getComponent(cc.Label).string = 'PLAY FASTER BY\nREDUCING TOTAL SIN TIME'
            this.Award_info.node.setPosition(0, -20)
            this.AUTOSTOPSETTINGS.node.active = false;
            this.InfoPageView[0].active = true;
            this.InfoPageView[1].active = false;


        }
        else {

            this.LongBarBG.enabled = false;
            this.BottomInfoBar.enabled = true;
            this.TopInfoBar.enabled = true;


            this.AUTOPLAYSETTINGPAGE.spriteFrame = this.AutoSettingBG[1];
            this.SYSTEM_SETTINGPAGE.spriteFrame = this.AutoSettingBG[1];
            // let description = cc.find('Titles/QUICK SPIN/New Label', this.SYSTEM_SETTINGPAGE.node)
            // description.getComponent(cc.Label).string = 'PLAY FASTER BY\nREDUCING TOTAL SIN TIME'
            this.Award_info.node.setPosition(0, 0)
            // EventSystem.emit(this.node,EventType.UpdateUI_AwardInfo)
            this.AUTOSTOPSETTINGS.node.active = true;
            // this.lastbtn.node.setPosition(-441.64, 0)
            // this.nextbtn.node.setPosition(430.463, 0)
            this.InfoPageView[0].active = false;
            this.InfoPageView[1].active = true;
        }
        // this.Award_info.updateLayout();


    }




}
