import { RuntimeHierarchy } from "./RuntimeHierarchy";

const { ccclass, property } = cc._decorator;

@ccclass
export class RuntimeInspector extends cc.Component {
    @property(RuntimeHierarchy)
    private m_RuntimeHierarchy: RuntimeHierarchy = null;

    @property(cc.Color)
    backgroundColor: cc.Color = cc.color(254, 251, 216);

    @property(cc.Float)
    opacity: number = .8;

    private m_Root: HTMLElement = null;
    private m_PositionInfo: HTMLElement = null;
    private m_SelectingNode: cc.Node = null;

    start() {
        this.createRoot();
        this.createPositionInfo();
        this.m_RuntimeHierarchy.onSelectNode = this.onSelectNode;
    }

    private onSelectNode = (node: cc.Node) => {

        if (null != this.m_SelectingNode) {
            this.m_SelectingNode.off(cc.Node.EventType.POSITION_CHANGED);
            this.m_SelectingNode.off(cc.Node.EventType.ROTATION_CHANGED);
            this.m_SelectingNode.off(cc.Node.EventType.SIZE_CHANGED);
        }

        this.m_SelectingNode = node;
        this.updateInspector();
    }

    lateUpdate() {
        this.updateInspector();
    }

    private updateInspector() {
        if (!this.m_SelectingNode)
            return;
        let node = this.m_SelectingNode;
        let pos = node.position;
        this.m_PositionInfo.innerText =
            `${node.name}
            ----------------
            POSITION{x:${pos.x},y:${pos.y},z:${pos.z}}
            ----------------
            ROTATION{x:${node.eulerAngles.x},y:${node.eulerAngles.y},z:${node.eulerAngles.z}}
            ----------------
            SIZE{width:${node.width},height:${node.height}}
            ----------------
            OPACITY:${node.opacity}
            ----------------
            SCALE:{${node.scaleX},${node.scaleY}}
            ----------------
            ZIndex:{${node.zIndex}
            `
    }

    private updatePositionInfo(pos: cc.Vec3) {

    }

    private createPositionInfo() {
        this.m_PositionInfo = document.createElement("label");
        this.m_PositionInfo.style.color = "black";
        this.m_Root.appendChild(this.m_PositionInfo);
    }

    private createRoot() {
        this.m_Root = document.createElement("div");
        this.m_Root.style.backgroundColor = this.backgroundColor.toHEX("#rrggbb");
        this.m_Root.style.opacity = this.opacity.toString();
        this.m_Root.style.position = "absolute";
        this.m_Root.style.left = "300px";
        this.m_Root.style.top = "35px";
        this.m_Root.style.maxHeight = "95%";
        this.m_Root.style.overflow = "auto";
        this.m_Root.style.padding = "5px";
        document.body.appendChild(this.m_Root);
        
    }

    onDestroy() {
        this.m_Root.parentElement.removeChild(this.m_Root);
    }
}
