import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";
import RemoteAssetManager from "../RemoteAssetManager";

import CommonUI_ViewController from "./CommonUI_ViewController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FreeGameCutSceneIn_ViewController extends cc.Component {

    @property(cc.Node)
    offset_TimesNode: cc.Node = null;
    @property(cc.Sprite)
    target: cc.Sprite = null;
    @property([cc.SpriteFrame])
    title_background: cc.SpriteFrame[] = []



    init() {


        if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
            this.target.spriteFrame = this.title_background[0]
            this.offset_TimesNode.setPosition(0, 52)
        }
        else {
            this.target.spriteFrame = this.title_background[1]
            this.offset_TimesNode.setPosition(0, 52)
        }




        EventSystem.on(EventType.ScreenChange, () => {

            if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
                this.target.spriteFrame = this.title_background[0]
                this.offset_TimesNode.setPosition(0, 52)
            }
            else {
                this.target.spriteFrame = this.title_background[1]
                this.offset_TimesNode.setPosition(0, 52)
            }
        })
    }

}
