const { ccclass, property } = cc._decorator;

@ccclass
export class RuntimeHierarchy extends cc.Component {

    @property(cc.Color)
    backgroundColor: cc.Color = cc.color(254, 251, 216);

    @property(cc.Color)
    highlightColor: cc.Color = cc.Color.GRAY;

    @property(cc.Float)
    opacity: number = .8;

    public onSelectNode: (node: cc.Node) => void;

    private m_Root: HTMLElement = null;
    private m_Div: HTMLElement = null;
    /** */
    private m_UnfoldingElems: string[] = [];

    start() {
        this.createRoot();
        this.createTitle();
        this.createDiv();
        this.initHierarchy();
        this.addStyle();

        let gameDiv = document.getElementById("GameDiv")
        gameDiv.style.right = "10px";
        gameDiv.style.top = "35px";
        gameDiv.style.position = "absolute";
        gameDiv.setAttribute("align", "right");

    }

    initHierarchy() {
        let scene = cc.director.getScene();
        scene.children.forEach(child => {
            let btn = this.createNodeElem(child);
            this.m_Div.appendChild(btn);
        });

        scene.on(cc.Node.EventType.CHILD_REORDER, () => this.refreshChildrenElements(this.m_Div, scene));
        scene.on(cc.Node.EventType.CHILD_ADDED, () => this.refreshChildrenElements(this.m_Div, scene));
        scene.on(cc.Node.EventType.CHILD_REMOVED, () => this.refreshChildrenElements(this.m_Div, scene));
    }

    isFolding(uuid: string): boolean {
        return !this.m_UnfoldingElems.has(uuid);
    }

    getElemName(node: cc.Node): string {
        if (!this.isFolding(node.uuid))
            return " " + node.name;
        else {
            let prefix = node.childrenCount > 0 ? " " : " ";
            return prefix + node.name;
        }
    }

    refreshChildrenElements(rootElem: HTMLElement, rootNode: cc.Node) {
        if (this.isFolding(rootNode.uuid))
            return;
        this.removeFoldRecodeRecursive(rootNode);
        rootElem.removeAllChild();
        rootElem.innerText = this.getElemName(rootNode);
        rootNode.children.forEach(child => {
            let btn = this.createNodeElem(child);
            rootElem.appendChild(btn);
        });
    }

    onClickNodeBtn = (elem: HTMLElement, node: cc.Node) => {
        if (this.onSelectNode)
            this.onSelectNode(node);

        if (node.childrenCount <= 0)
            return;
        //console.log("Click", node.name, "_", node.uuid);
        let uuid = node.uuid;
        if (this.isFolding(uuid)) {
            this.m_UnfoldingElems.push(uuid);
            elem.innerText = this.getElemName(node);

            node.children.forEach((child) => {
                let btn = this.createNodeElem(child);
                elem.appendChild(btn);
            });
        }
        else {
            this.m_UnfoldingElems.remove(uuid);
            elem.innerText = this.getElemName(node);
            elem.removeAllChild();
            this.removeFoldRecodeRecursive(node);
        }


    }

    removeFoldRecodeRecursive(node: cc.Node) {
        node.children.forEach((child) => {
            this.m_UnfoldingElems.remove(child.uuid);
            this.removeFoldRecodeRecursive(child);
        });
    }

    getTextColor(node: cc.Node): string {
        return node.active ? "black" : "gray";
    }

    createTitle() {
        let lbl = document.createElement("label");
        lbl.innerText = "Runtime Hierarchy";
        lbl.style.color = "black";
        lbl.style.textAlign = "center";
        lbl.style.textDecoration = "bold";

        let hr = document.createElement("hr");
        this.m_Root.appendChild(lbl);
        this.m_Root.appendChild(hr);
    }

    createDiv() {
        this.m_Div = document.createElement("div");
        this.m_Div.className = "RuntimeHierarchy_Nodes";
        this.m_Root.appendChild(this.m_Div);
    }

    createNodeElem(node: cc.Node): HTMLElement {
        let div = document.createElement("div");
        div.className = "node";
        div.style.display = "block";

        let btn = this.createNodeButton(node);
        let activeToggle = this.createToggle(node, btn);

        div.appendChild(btn);
        div.appendChild(activeToggle);
        return div;
    }

    createNodeButton(node: cc.Node): HTMLButtonElement {
        let btn = document.createElement("button");
        btn.style.display = "inline-block";
        btn.style.border = "none";
        btn.style.textAlign = "left";
        btn.style.backgroundColor = "inherit";
        btn.innerText = this.getElemName(node);
        btn.style.color = this.getTextColor(node);

        btn.onclick = (ev: MouseEvent) => {
            ev.cancelBubble = true;
            this.onClickNodeBtn(btn, node);
        };

        let refreshCallback = () => this.refreshChildrenElements(btn, node);
        node.on(cc.Node.EventType.CHILD_REORDER, refreshCallback);
        node.on(cc.Node.EventType.CHILD_ADDED, refreshCallback);
        node.on(cc.Node.EventType.CHILD_REMOVED, refreshCallback);
        node.on("active-in-hierarchy-changed", () => console.log("Active Canged", node.name));
        return btn;
    }

    createToggle(node: cc.Node, btn: HTMLElement): HTMLInputElement {
        let tgl = document.createElement("input");
        tgl.type = "checkbox";
        tgl.checked = node.active;
        tgl.onclick = (ev: Event) => {
            ev.cancelBubble = true;
            node.active = !node.active;
            btn.style.color = this.getTextColor(node);
        };
        return tgl;
    }

    createRoot() {
        this.m_Root = document.createElement("div");
        this.m_Root.style.backgroundColor = this.backgroundColor.toHEX("#rrggbb");
        this.m_Root.style.opacity = this.opacity.toString();
        this.m_Root.style.position = "absolute";
        this.m_Root.style.left = "10px";
        this.m_Root.style.top = "35px";
        this.m_Root.style.maxHeight = "95%";
        this.m_Root.style.overflow = "auto";
        this.m_Root.style.padding = "5px";
        document.body.appendChild(this.m_Root);
    }

    onDestroy() {
        this.m_Root.parentElement.removeChild(this.m_Root);
    }

    addStyle() {
        let style = document.createElement("style");
        let css = `\n.node {
            background-color: inherit;
            display = block;
            border = none;
            textAlign = left;
                    }
                    .node:hover {
            background-color: ${this.highlightColor.toHEX("#rrggbb")};
        }
        .node.parent {  
            pointer-events: none;
            background-color: #2100dd;
        }
        .node.parent:hover {  
            pointer-events: none;
            background-color: #2100dd;
        }
        .node.child {  
            pointer-events: none;
            background-color: #2100dd;
                    }

        `;
        style.innerHTML = css;
        document.head.appendChild(style);
    }
}
