import GameServer from "./FakeServer/GameServer";
import GameManager, { GameScreenLayout } from "./GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export class NumImgJumper extends cc.Component {

    @property(cc.Node)
    ParentNode: cc.Node = null;

    @property([cc.SpriteFrame])
    numSpriteArray: cc.SpriteFrame[] = [];

    @property(cc.Boolean)
    needPrecisionStr: boolean = true;


    @property
    numImgScale = 1;

    private m_Time = 0;
    private m_Duration = 0;
    private m_StartNum = 0;
    private m_EndNum = 0;
    private _callback: () => void;


    startJumpingNum(start: number, end: number, duration: number, callback?: () => void) {
        if (callback != undefined)
            this._callback = callback;

        this.m_StartNum = start;
        this.m_EndNum = end;
        this.m_Duration = duration;
        this.m_Time = 0;
    }

    lateUpdate(dt) {

        let scale = 0.5;
        if (this.m_Time < this.m_Duration) {
            this.m_Time = Math.min(this.m_Duration, this.m_Time + dt);
            let t = cc.misc.clamp01(this.m_Time / this.m_Duration);
            let num = cc.misc.lerp(this.m_StartNum, this.m_EndNum, t);
            let valueStr = ''
            if (this.needPrecisionStr && GameServer.self.getTotalBet() < 100) {
                valueStr = num.toPrecisionStr(2);
            }
            else {
                valueStr = num.toPrecisionStr(0)
            }

            //console.log(valueStr);

            if (valueStr.length <= 7)
                scale = 1
            else {
                let length = valueStr.length - 7
                scale = 1 - (length * 0.05)
            }

            this.updateNumImgs(valueStr, scale);


        }
        else if (this._callback != undefined) {
            this._callback();
            this._callback = undefined;
        }
    }

    updateNumImgs(valueStr: string, scale) {
        let parent = this.ParentNode

        if (parent != null) {
            parent.scale = scale
            parent.destroyAllChildren();
            for (const char of valueStr) {
                let charNode = new cc.Node();
                charNode.setParent(parent);
                charNode.scale = this.numImgScale;
                let spr = charNode.addComponent(cc.Sprite);
                spr.trim = false;
                spr.sizeMode = cc.Sprite.SizeMode.RAW
                let tex = this.getSpriteByChar(char);
                if (tex)
                    spr.spriteFrame = tex;
            }

        }
    }

    getSpriteByChar(char: string): cc.SpriteFrame {
        // console.log(char)
        if (char.match(/[0-9]/)) {
            let num = Number.parseInt(char);
            return this.numSpriteArray[num];
        }
        else if (char === ',')
            return this.numSpriteArray[10];
        else if (char === '.')
            return this.numSpriteArray[11];

        return null;
    }

}
