const { ccclass, property } = cc._decorator;

@ccclass
export default class Convert extends cc.Component {
    @property(cc.Node)
    A: cc.Node = null;

    @property(cc.Node)
    A_child: cc.Node = null;

    @property(cc.Node)
    B: cc.Node = null;

    @property(cc.Node)
    B_child: cc.Node = null;

    @property(cc.Node)
    T: cc.Node = null;

    @property([cc.Node])
    T_child: cc.Node[] = [];

    @property(cc.Button)
    toV: cc.Button = null;

    @property(cc.Button)
    toH: cc.Button = null;

    start() {
        this.toV.node.on('click', () => { this.convern_Horizontal_To_Vertical(this.T_child[0]) })
        this.toH.node.on('click', () => { this.convern_Vertical_To_Horizontal(this.T_child[0]) })

    }


    convern_Horizontal_To_Vertical(Original: cc.Node,targetNode?:cc.Node) {

        let OriginalWorldPos = Original.convertToWorldSpaceAR(cc.v2(0, 0))
        let target = targetNode.convertToWorldSpaceAR(cc.v2(0, 0))

        cc.log(`OriginalWorldPos=${OriginalWorldPos} \ntarget=${target}`)

        let result = cc.v2(target.x - OriginalWorldPos.x, target.y - OriginalWorldPos.y)
    
        cc.log(result)
        Original.x+=result.x
        Original.y+=result.y

    }

    convern_Vertical_To_Horizontal(Original: cc.Node,targetNode?:cc.Node) {

        let OriginalWorldPos = Original.convertToWorldSpaceAR(cc.v2(0, 0))

        let target = targetNode.convertToWorldSpaceAR(cc.v2(0, 0))

        cc.log(`OriginalWorldPos=${OriginalWorldPos} \ntarget=${target}`)

        let result = cc.v2(target.x - OriginalWorldPos.x, target.y - OriginalWorldPos.y)
        
        cc.log(result)
        Original.x+=result.x
        Original.y+=result.y

    }


}
