
import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CheatMod extends cc.Component {


    lbl_currentMode: cc.Label = null;
    btn_touchArea: cc.Button = null;
    currentModeIndex: number = 0;
    ModeLength = 0;




    // onLoad () {}

    start() {
        this.ModeLength = Object.entries(CheatEnum).length
        this.lbl_currentMode = this.node.getChildByName('lbl_currentmod').getComponent(cc.Label)
        this.btn_touchArea = this.lbl_currentMode.node.getChildByName('TouchArea').getComponent(cc.Button)
        this.btn_touchArea.node.on('click', () => {
            console.log('AAAAAAAA')
            this.currentModeIndex = (this.currentModeIndex + 1) % this.ModeLength
            GameManager.GM.ServerPortocolMgr.CheatModIndex = this.lbl_currentMode.string = Object.values(CheatEnum)[this.currentModeIndex]

        })
        this.lbl_currentMode.string = CheatEnum.Normal;
        EventSystem.on(EventType.ScreenChange, (layout: GameScreenLayout) => {
            switch (layout) {
                case GameScreenLayout.Horizontal:
                    this.lbl_currentMode.node.setPosition(-533, 341)
                    break;
                case GameScreenLayout.Vertical:
                    this.lbl_currentMode.node.setPosition(-280, 649)
                    break;


            }
        })

    }

    // update (dt) {}
}

export enum CheatEnum {
    Normal = 'Normal',
    FreeGame = 'FreeGame',

}
