

const { ccclass, property } = cc._decorator;

@ccclass
export default class MoneyBagSpine extends cc.Component {

    @property(sp.Skeleton)
    anim: sp.Skeleton = null;

    @property([String])
    animName: string[] = []

    @property(Boolean)
    isLoopLast: boolean = false;

    start() {

    }

    protected onEnable(): void {
        this.anim.setAnimation(0, this.animName[0], false)
        this.anim.setCompleteListener(() => {
            this.anim.setAnimation(0, this.animName[1], false)
        })

    }
    protected onDisable(): void {
        this.anim.setAnimation(0, this.animName[0], false)
    }

    // update (dt) {}
}
