

import GameManager from "./GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FSTimes extends cc.Component {


    FSLabel: cc.Label = null;

    @property(cc.Animation)
    Tens: cc.Animation = null

    @property(cc.Animation)
    digital: cc.Animation = null

    FS: cc.Animation = null

    UpdateAnimation() {
        this.FSLabel = this.node.getComponent(cc.Label)
        if (this.Tens.getComponent(cc.Animation) == null)
            this.Tens = this.Tens.addComponent(cc.Animation)
        if (this.digital.getComponent(cc.Animation) == null)
            this.digital = this.digital.addComponent(cc.Animation)

        if (this.Tens.getComponent(cc.Sprite) == null)
            this.Tens.addComponent(cc.Sprite)
        if (this.digital.getComponent(cc.Sprite) == null)
            this.digital.addComponent(cc.Sprite)


        for (let i = 0; i < this.FSLabel.string.length; i++) {
            this.Tens._defaultClip = this.getAnimationClipByChar(this.FSLabel.string[i])
            if (i > 0)
                this.digital._defaultClip = this.getAnimationClipByChar(this.FSLabel.string[i])
            else { this.digital._defaultClip = null }

            console.log(this.Tens.defaultClip.name)
            console.log(this.digital.defaultClip.name)

        }

    }

    getAnimationClipByChar(char: string): cc.AnimationClip {
        console.log(char)
        if (char.match(/[0-9]/)) {
            let num = Number.parseInt(char);
            return GameManager.GM.FS_TimesDigital.FreeSpinTimes_Digital[num]
        }

        return null;
    }


    // update (dt) {}
}
