import GameServer from "../FakeServer/GameServer";
import GameManager from "../GameManager";
import MysteryData from "../Mystery/MysteryData";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Test extends cc.Component {

    @property(cc.Button)
    testBtn: cc.Button = null;

    start() {
        this.testBtn = this.node.getComponent(cc.Button)
        this.testBtn.node.on('click', () => {
            let BonusData: MysteryData = new MysteryData();
            BonusData.amount = 10
            BonusData.balance = GameServer.self.UserCredit + 10;
            BonusData.type = 0
            GameManager.GM.Mystery.mData.push(BonusData)
        })
    }

    // update (dt) {}
}
