const { ccclass, property } = cc._decorator;
export enum EventType {
    Normal = 'Normal',
    Running = "Running",
    Stoped = "Stoped",
    ShowResult = "ShowResult",
    ShowResultEnd = "ShowResultEnd",
    CheckExpand = "CheckExpand",
    ShowBetReward = "ShowBetReward",
    ShowBetReward_End = "ShowBetReward_End",
    CheckBigWin = "CheckBigWin",
    BackToNormal = "BackToNormal",

    BigWininit = "BigWininit",
    UpdateUI = "UpdateUI",
    CHANGE_STATE = "CHANGE_STATE",
    onEnterFreeGame = 'onEnterFreeGame',
    FreeGameState = "FreeGameState",
    FreeGameEnd = "FreeGameEnd",
    BigWinShowEnd = "BigWinShowEnd",
    ScreenChange = "ScreenChange",
    CheckCanBet = "CheckCanBet",

    AssetLoaded = "AssetLoaded",
    Gameinit = "Gameinit",
    clientReady = "clientReady",

    BonusState = "BonusState",
    BonusExit = "BonusExit",
    hasMystery = "hasMystery"
}

@ccclass
export class EventSystem extends cc.Component {

    private static _Instance: EventSystem;

    public static get Node(): cc.Node {
        return this._Instance.node;
    }

    public static on<T extends Function>(type: string, callback: T, target?: any, useCapture?: boolean): T {
        return EventSystem.Node.on(type, callback, target, useCapture);
    }

    public static off(type: string, callback?: Function, target?: any, useCapture?: boolean): void {
        EventSystem.Node.off(type, callback, target, useCapture);
    }

    public static targetOff(target: any) {
        EventSystem.Node.targetOff(target);
    }

    public static emit(Node: cc.Node, type: string, arg1?: any, arg2?: any, arg3?: any, arg4?: any, arg5?: any): void {
        // cc.log(`%c${Node.name} ChangeState --> %c${type}`,
        //     'color:#95f153; font-weight:bold; background-color:#000040', "color:#f7c633;background-color:#000040");

        EventSystem.Node.emit(type, arg1, arg2, arg3, arg4, arg5);
    }

    onLoad() {
        EventSystem._Instance = this;


    }
}
