import { AudioManager } from "../Audio/AudioManager";
import { SoundType } from "../Audio/AudioType";
import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class infopage extends cc.Component {

    @property(cc.Node) backButton: cc.Node = null;
    @property(cc.Node) nextButton: cc.Node = null;
    @property(cc.Node) closeButton: cc.Node = null;
    m_pageview: cc.PageView = null;
    m_pageviewLength: number = 0;
    @property(cc.Node) infopage: cc.Node = null;

    @property(cc.Button)
    tbtn: cc.Button = null;

    @property(cc.SpriteFrame)
    PageViewBottomSprs: cc.SpriteFrame[] = [];

    @property(cc.Node)// 40 PayLine
    PayLinePageNumber: cc.Node = null;

    // @property(cc.Sprite)
    // PageViewBottomBG: cc.Sprite = null;

    initScreen(Layout) {
        if (Layout == GameScreenLayout.Horizontal) {
            // this.PageViewBottomBG.spriteFrame = this.PageViewBottomSprs[0]
            // this.PageViewBottomBG.node.setContentSize(1334, 750)
        }
        else {
            // this.PageViewBottomBG.spriteFrame = this.PageViewBottomSprs[1]
            // this.PageViewBottomBG.node.setContentSize(750 / 0.75, 1334 / 0.75)
        }

        for (let i = 0; i < this.PayLinePageNumber.children.length; i++) {
            this.PayLinePageNumber.children[i].getComponent(cc.Label).string = (i + 1).toString();
        }

    }

    update() {
        // console.log(this.tbtn.node.getPosition())

    }

    onLoad() {
        this.initScreen(GameManager.GM.currentGameScreenLayout)

        EventSystem.on(EventType.ScreenChange, (Layout: GameScreenLayout) => {
            if (Layout == GameScreenLayout.Horizontal) {
                // this.PageViewBottomBG.spriteFrame = this.PageViewBottomSprs[0]
                // this.PageViewBottomBG.node.setContentSize(1334, 750)
                // this.PageViewBottomBG.node.setPosition(0, 0)
            }
            else {
                // this.PageViewBottomBG.spriteFrame = this.PageViewBottomSprs[1]
                // this.PageViewBottomBG.node.setContentSize(750 / 0.75, 1334 / 0.75)
                // this.PageViewBottomBG.node.setPosition(0, -27)
                // this.closeButton.setPosition(750 / 2, (1334 / 2) - 50)
            }
        })
        this.closeButton.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            // this.closeButton.getComponentInChildren(cc.Sprite).node.setPosition(0, 0)
            this.infopage.active = false
        });

        // this.backButton.active = false;
        this.m_pageview = this.node.getComponent(cc.PageView);
        this.m_pageviewLength = this.m_pageview.node.getChildByName('view').getChildByName('content').children.length

        this.backButton.on('click', () => {
            if (this.m_pageview.getCurrentPageIndex() - 1 < 0)
                this.m_pageview.setCurrentPageIndex(this.m_pageviewLength - 1)
            else {
                this.m_pageview.setCurrentPageIndex(this.m_pageview.getCurrentPageIndex() - 1)
            }

            AudioManager.playSound(SoundType.CLICK_BUTTON)

        })
        this.nextButton.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            if (this.m_pageview.getCurrentPageIndex() + 1 > this.m_pageviewLength - 1)
                this.m_pageview.setCurrentPageIndex(0)
            else {
                this.m_pageview.setCurrentPageIndex(this.m_pageview.getCurrentPageIndex() + 1)
            }
            // if (this.m_pageview.getCurrentPageIndex() == 0) this.backButton.active = false;
            // else this.backButton.active = true;
            // if (this.m_pageview.getCurrentPageIndex() == this.m_pageview.content.childrenCount - 1) this.nextButton.active = false;
            // else this.nextButton.active = true;
        })

    }


}



