import GameServer from "./FakeServer/GameServer";

const { ccclass, property } = cc._decorator;

@ccclass
export class BubbleNumberJumper extends cc.Component {

    numSpriteAtlas: cc.SpriteFrame[] = []
    numImgScale = 0.25;

    private m_Time = 0;
    private m_Duration = 0;
    private m_StartNum = 0;
    private m_EndNum = 0;
    private m_DecimalNum = 2;
    private m_Color = cc.Color.WHITE;
    private _callback: () => void;

    startJumpingNum(start: number, end: number, duration: number, decimalNum: number, callback?: () => void) {
        if (callback != undefined)
            this._callback = callback;

        this.m_StartNum = start;
        this.m_EndNum = end;
        this.m_Duration = duration;
        this.m_Time = 0;
        this.m_DecimalNum = decimalNum;
    }

    lateUpdate(dt) {
        if (this.m_Time < this.m_Duration) {
            let scale = 0.5;
            this.m_Time = Math.min(this.m_Duration, this.m_Time + dt);
            let t = cc.misc.clamp01(this.m_Time / this.m_Duration);
            let num = cc.misc.lerp(this.m_StartNum, this.m_EndNum, t);
            let valueStr = ''
            if (num < 10000) {
                if (num < 1000)
                    valueStr = num.toPrecisionStr(2)
                else {
                    valueStr = num.toString();

                }
            }
            else {
                valueStr = ((num) / 1000).toString() + 'k'
            }
            // switch (valueStr.length) {
            //     case 4:
            if (valueStr.length <= 7)
                scale = 0.4
            else
                scale = 0.35
            //         break;
            //     case 5:
            //         scale = 0.35
            //         break;
            //     case 6:
            //         scale = 0.30
            //         break;
            //     // default:
            //     //     scale = 0.5
            //     //     break;
            // }

            //console.log(valueStr);
            this.updateNumImgs(valueStr, scale);
        }
        else if (this._callback != undefined) {
            this._callback();
            this._callback = undefined;
        }
    }

    setNumImgsColor(_color: cc.Color) {
        this.m_Color = _color;
    }

    updateNumImgs(valueStr: string, scale) {

        let parent = this.node.getChildByName('num')
        let layout: cc.Layout = null;
        layout = parent.getComponent(cc.Layout)
        layout.spacingX = -25
        layout.type = cc.Layout.Type.HORIZONTAL
        layout.resizeMode = cc.Layout.ResizeMode.CONTAINER
        layout.affectedByScale = true;
        layout.node.anchorY = 0.5
        parent.scale = scale
        if (parent != null) {

            parent.destroyAllChildren();
            for (const char of valueStr) {
                let charNode = new cc.Node();
                charNode.setParent(parent);
                // charNode.scale = this.numImgScale;
                let spr = charNode.addComponent(cc.Sprite);
                let tex = this.getSpriteByChar(char);
                if (tex)
                    spr.spriteFrame = tex;
                charNode.color = this.m_Color;
            }
        }
    }

    getSpriteByChar(char: string): cc.SpriteFrame {
        if (char.match(/[0-9]/)) {
            let num = Number.parseInt(char);
            return this.numSpriteAtlas[num]
        }
        else if (char === ',')
            return this.numSpriteAtlas[10];
        else if (char === '.')
            return this.numSpriteAtlas[11]
        else if (char === 'k')
            return this.numSpriteAtlas[12]

        return null;
    }
}
