const { ccclass, property } = cc._decorator;

@ccclass
export default class Cryptor extends cc.Component {
  secret: string = null;

  constructor(passwd?: string) {
    super();
    this.secret = passwd;
  }

  encrypt(src) {
    return src;
  }

  decrypt(encryped) {
    return encryped;
  }
}
