import { AudioManager } from "../Audio/AudioManager";
import { SoundType } from "../Audio/AudioType";
import { EventType } from "../EventSystem";
import GameServer from "../FakeServer/GameServer";
import GameManager from "../GameManager";
import UI_interface from "./UI_interface";

const { ccclass, property } = cc._decorator;

@ccclass
export default class AutoPlay_Setting extends cc.Component {

    @property({ type: cc.Float, displayName: '+- ' })
    MaxForceTime: number = 0.5;

    m_Time: number = 0;
    m_IsTouching: boolean = false;
    force: number = 0;
    shouldPlaySound: boolean = false;
    m_fastmode: boolean = false;

    @property(cc.Button)
    btn_Confirm: cc.Button = null;

    @property(cc.Button)
    btn_close: cc.Button = null;

    @property({ type: [cc.Button], displayName: '' })
    btn_Set_autotimes: cc.Button[] = [];

    @property(cc.Label)
    lbl_Current_loopTime: cc.Label = null;

    setting_looptimes: number = 0;
    SmallerCredit: number = 0;
    BiggerCredit: number = 0;

    @property(cc.Button)
    btn_BiggerAdd: cc.Button = null;

    @property(cc.Button)
    btn_BiggerSub: cc.Button = null;

    @property(cc.Button)
    btn_SmallerAdd: cc.Button = null;

    @property(cc.Button)
    btn_SmallerSub: cc.Button = null;

    @property(cc.Label)
    lbl_Biggerlimit: cc.Label = null;

    @property(cc.Label)
    lbl_Smallerlimit: cc.Label = null;

    @property(cc.Toggle)
    tgl_Bigger: cc.Toggle = null;

    @property(cc.Toggle)
    tgl_Smaller: cc.Toggle = null;

    @property(cc.Toggle)
    tgl_bonus: cc.Toggle = null;

    @property(cc.Toggle)
    tgl_skipscreen: cc.Toggle = null;

    @property(cc.Toggle)
    tgl_quickspin: cc.Toggle = null;

    @property(cc.Node)
    node_unlimted: cc.Node = null;



    m_eachtime_ChangeValue: number = 0;
    btnname: string = '';
    isBonusContinue: boolean = false;
    times = [10, 30, 50, 100, 200, 1000];


    setButton(button, value) {

        button.node.on(cc.Node.EventType.TOUCH_START, () => {
            this.btnname = button.node.name;
            this.m_IsTouching = true;
            this.m_eachtime_ChangeValue = value
        });
        button.node.on(cc.Node.EventType.TOUCH_END, () => {
            this.m_IsTouching = false;
            this.m_Time = 0;
        });
        button.node.on(cc.Node.EventType.TOUCH_CANCEL, () => {
            this.m_IsTouching = false;
            this.m_Time = 0;
        });

    }

    init() {
        // this.init_AutoPlaySetting();

        //
        for (let i = 0; i < this.btn_Set_autotimes.length; i++) {
            this.btn_Set_autotimes[i].node.on('click', () => {
                AudioManager.playSound(SoundType.CLICK_BUTTON)
                if (i != this.btn_Set_autotimes.length - 1) {
                    this.setting_looptimes = this.times[i];
                    this.lbl_Current_loopTime.string = `${UI_interface.self.language_assets.START_AUTOPLAY} (${this.times[i].toString()})`
                }
                if (i == this.btn_Set_autotimes.length - 1) {
                    this.setting_looptimes = this.times[i]
                    this.lbl_Current_loopTime.string = `${UI_interface.self.language_assets.START_AUTOPLAY} ()`;
                    // this.node_unlimted.active = true;
                }
                // else this.node_unlimted.active = false
            })

        };



        this.tgl_quickspin.node.on('toggle', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            UI_interface.self.QuickSpinFlag = !UI_interface.self.QuickSpinFlag
            UI_interface.self.AutoSetting.tgl_quickspin.isChecked = UI_interface.self.SystemSetting.tgl_quickspin.isChecked = UI_interface.self.QuickSpinFlag
            GameManager.GM.Instance_WheelManager.resetStopPara();
        })

        //+
        this.btn_BiggerAdd.node.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            this.BiggerCredit += 10;
            this.lbl_Biggerlimit.string = this.BiggerCredit.toPrecisionStr(2)
            // cc.Sound.playKeySound(cc.Sound.publicKeySound)



            //  
            if (this.BiggerCredit > GameServer.self.UserCredit) this.btn_BiggerSub.interactable = true;

        })
        //-
        this.btn_BiggerSub.node.on('click', () => {
            if (this.BiggerCredit > GameServer.self.UserCredit) {
                this.BiggerCredit -= 10;
                AudioManager.playSound(SoundType.CLICK_BUTTON)
                this.lbl_Biggerlimit.string = this.BiggerCredit.toPrecisionStr(2)

                if (this.BiggerCredit <= GameServer.self.UserCredit) {
                    this.btn_BiggerSub.interactable = false;
                }
            }
        })

        //+
        this.btn_SmallerAdd.node.on('click', () => {
            if (this.SmallerCredit < GameServer.self.UserCredit) {
                this.SmallerCredit += 10;
                AudioManager.playSound(SoundType.CLICK_BUTTON)
                this.lbl_Smallerlimit.string = this.SmallerCredit.toPrecisionStr(2)
                if (this.SmallerCredit == GameServer.self.UserCredit) {
                    this.btn_SmallerAdd.interactable = false;
                }
            }
        })

        //-
        this.btn_SmallerSub.node.on('click', () => {
            if (this.SmallerCredit > 0) {
                this.SmallerCredit -= 10;
                AudioManager.playSound(SoundType.CLICK_BUTTON)
            }
            this.lbl_Smallerlimit.string = this.SmallerCredit.toPrecisionStr(2)
            if (this.SmallerCredit <= 0) {
                this.btn_SmallerSub.interactable = false;
            }
            if (this.SmallerCredit < GameServer.self.UserCredit)
                this.btn_SmallerAdd.interactable = true;

        })

        // OK
        this.btn_Confirm.node.on('click', () => {

            if (this.setting_looptimes <= 0) {
                this.node.getChildByName('Page_Auto()').active = this.node.getChildByName('tgl_auto').getComponent(cc.Toggle).isChecked = false;
                return;
            }

            if (this.setting_looptimes > 0) {
                if (this.tgl_Bigger.isChecked) {
                    UI_interface.self.BiggerSetting = this.BiggerCredit
                    UI_interface.self.BiggerTag = true;
                }
                if (this.tgl_Smaller.isChecked) {
                    UI_interface.self.SmallerSetting = this.SmallerCredit
                    UI_interface.self.SmallerTag = true;
                }
                UI_interface.self.LoopTimes = this.setting_looptimes
                // cc.Sound.playKeySound(cc.Sound.publicKeySound)
                AudioManager.playSound(SoundType.CLICK_BUTTON)
                this.node.getChildByName('tgl_auto').getComponent(cc.Toggle).isChecked = false;
                this.SmallerCredit = this.BiggerCredit = GameServer.self.UserCredit;
                this.lbl_Smallerlimit.string = this.lbl_Biggerlimit.string = GameServer.self.UserCredit.toPrecisionStr(2)
                AudioManager.playSound(SoundType.CLICK_BUTTON)
            }

            this.node.getChildByName('Page_Auto()').active = this.node.getChildByName('tgl_auto').getComponent(cc.Toggle).isChecked = false;


            if (GameManager.GM.GameState == EventType.Normal) {

                GameManager.GM.Spin.node.emit('click')
            }

            if (this.setting_looptimes <= 200)
                this.setting_looptimes -= 1
            UI_interface.self.LoopTimes = this.setting_looptimes
            UI_interface.self.Update_UI();
            this.init_AutoPlaySetting();

        });

        this.setButton(this.btn_SmallerAdd, 100);
        this.setButton(this.btn_SmallerSub, -100);
        this.setButton(this.btn_BiggerAdd, 100);
        this.setButton(this.btn_BiggerSub, -100);


        this.tgl_bonus.node.on('toggle', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            //cc.Sound.playKeySound(cc.Sound.publicKeySound) 
            this.isBonusContinue = this.tgl_bonus.isChecked; //Bonus
        });

        this.tgl_skipscreen.node.on('toggle', () => {
            // this.tgl_skipscreen.isChecked = !this.tgl_skipscreen.isChecked;
            GameManager.GM.isSkipMode = this.tgl_skipscreen.isChecked;
            AudioManager.playSound(SoundType.CLICK_BUTTON)
        });

        this.tgl_Bigger.node.on('toggle', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
        })
        this.tgl_Smaller.node.on('toggle', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
        })

        //XX
        this.btn_close.node.on('click', () => {
            this.node.getChildByName('tgl_auto').getComponent(cc.Toggle).isChecked = false;
            this.node.getChildByName('Page_Auto()').active = false;
            this.BiggerCredit = GameServer.self.UserCredit;
            this.lbl_Biggerlimit.string = this.BiggerCredit.toPrecisionStr(2)
            this.SmallerCredit = GameServer.self.UserCredit;
            this.lbl_Smallerlimit.string = this.SmallerCredit.toPrecisionStr(2)

            this.BiggerCredit = GameServer.self.UserCredit;
            this.lbl_Biggerlimit.string = this.BiggerCredit.toPrecisionStr(2)
            this.btn_BiggerSub.interactable = false

            this.SmallerCredit = GameServer.self.UserCredit;
            this.lbl_Smallerlimit.string = this.SmallerCredit.toPrecisionStr(2)
            this.btn_SmallerAdd.interactable = false
            AudioManager.playSound(SoundType.CLICK_BUTTON)

        });


    }

    init_AutoPlaySetting() {
        //


        this.BiggerCredit = GameServer.self.UserCredit;
        this.lbl_Biggerlimit.string = this.BiggerCredit.toPrecisionStr(2)

        this.SmallerCredit = GameServer.self.UserCredit;
        this.lbl_Smallerlimit.string = this.SmallerCredit.toPrecisionStr(2)

        this.btn_BiggerSub.interactable = false
        this.btn_SmallerAdd.interactable = false

        this.tgl_Bigger.isChecked = false;
        this.tgl_Smaller.isChecked = false;
        this.setting_looptimes = 0;
        this.lbl_Current_loopTime.string = `${UI_interface.self.language_assets.START_AUTOPLAY} (0)`
    }


    eachtime_ChangeValue: number = 100

    update(dt) {
        if (this.m_IsTouching) {
            this.m_Time += dt;
            if (this.m_Time > this.MaxForceTime) {
                this.m_Time = 0.2;
                if (this.btnname == 'BiggerCredit+') {
                    if (this.BiggerCredit > GameServer.self.UserCredit) this.btn_BiggerSub.interactable = true;
                    this.BiggerCredit += this.eachtime_ChangeValue;
                    this.lbl_Biggerlimit.string = this.BiggerCredit.toPrecisionStr(2)
                    // cc.Sound.playKeySound(cc.Sound.publicKeySound)

                }
                if (this.btnname == 'BiggerCredit-') {
                    if (this.BiggerCredit > GameServer.self.UserCredit) {
                        this.BiggerCredit -= 100;
                        // cc.Sound.playKeySound(cc.Sound.publicKeySound)
                        this.lbl_Biggerlimit.string = this.BiggerCredit.toPrecisionStr(2)
                        if (this.BiggerCredit <= GameServer.self.UserCredit) {

                            this.btn_BiggerSub.interactable = false;
                            this.BiggerCredit = GameServer.self.UserCredit;
                            this.lbl_Biggerlimit.string = this.BiggerCredit.toPrecisionStr(2)

                        }

                    }
                }
                if (this.btnname == 'SmallerCredit+') {
                    if (this.SmallerCredit < GameServer.self.UserCredit) {
                        this.SmallerCredit += 100;
                        // cc.Sound.playKeySound(cc.Sound.publicKeySound)
                        this.lbl_Smallerlimit.string = this.SmallerCredit.toPrecisionStr(2)
                        if (this.SmallerCredit >= GameServer.self.UserCredit) {
                            this.btn_SmallerAdd.interactable = false;
                            this.SmallerCredit = GameServer.self.UserCredit;
                            this.lbl_Smallerlimit.string = this.SmallerCredit.toPrecisionStr(2)

                        }
                    }
                }
                if (this.btnname == 'SmallerCredit-') {
                    if (this.SmallerCredit > 0)
                        this.SmallerCredit -= 100;
                    // cc.Sound.playKeySound(cc.Sound.publicKeySound)
                    this.lbl_Smallerlimit.string = this.SmallerCredit.toPrecisionStr(2)
                    if (this.SmallerCredit < GameServer.self.UserCredit) this.btn_SmallerAdd.interactable = true;
                }


            }


        }
    }


}
