import { EventSystem, EventType } from "../EventSystem";
import CommonUI_ViewController from "./CommonUI_ViewController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class decorate extends CommonUI_ViewController {

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    Puppy: sp.Skeleton = null;
    Node_decorate: cc.Node = null;
    start() {
        this.Node_decorate = cc.find('Canvas/Mask/decorate')
        this.Puppy = this.Node_decorate.getChildByName('Puppy_dog').getComponent(sp.Skeleton)
        this.Puppy.defaultAnimation = 'basegame_dog_idle'
        EventSystem.on(EventType.onEnterFreeGame, () => {

            this.Puppy.setAnimation(0, 'freegame_dog_idle', true)
        })
        EventSystem.on(EventType.FreeGameEnd, () => {
            this.Puppy.setAnimation(0, 'basegame_dog_idle', true);
        })

    }
    other_commonUI_PanleLayoutSetting = () => { }
    // update (dt) {}
}
