import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";


const { ccclass, property } = cc._decorator;

@ccclass
export default class LOGO_ViewController extends cc.Component {


    @property(cc.Node)
    LOGO: cc.Node = null;
    @property(cc.Node)
    LOGO_hori: cc.Node = null;
    @property(cc.Node)
    LOGO_Verti: cc.Node = null;

    start() {
        EventSystem.on(EventType.ScreenChange, () => {
            if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
                this.LOGO.setPosition(this.LOGO_hori.getPosition(cc.v2()))
                this.LOGO.setScale(this.LOGO_hori.getScale(cc.v2()))
            }
            else {
                this.LOGO.setPosition(this.LOGO_Verti.getPosition(cc.v2()))
                this.LOGO.setScale(this.LOGO_Verti.getScale(cc.v2()))
            }
        })
    }
}
