import GameManager from "../GameManager";
import CommonUI_ViewController from "./CommonUI_ViewController";


const { ccclass, property } = cc._decorator;

@ccclass
export default class LoadingPage_ViewController extends cc.Component {

    LoadingPage: cc.Node = null

    ProgressBar: cc.ProgressBar = null

    protected onLoad(): void {

        this.LoadingPage = this.node;
        this.ProgressBar = this.node.getComponentInChildren(cc.ProgressBar);

        // GameManager.GM.DetectScreenSize()
        GameManager.GM.ChangeScreenSize(GameManager.GM.currentGameScreenLayout)

    }



}
