import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";

const { ccclass, property } = cc._decorator;
/*

UI 
CollectionTarget   CurrentNode[]
Horizontal:cc.Node 
Vertical:cc.Node 
-- --

.... 2021/8/30

*/
@ccclass
export default class CommonUI_ViewController extends cc.Component {


    @property(cc.Node)
    protected CollectionTarget: cc.Node = null;

    @property(cc.Node)
    protected Horizontal: cc.Node = null;

    @property(cc.Node)
    protected Vertical: cc.Node = null;

    @property(cc.Boolean)
    protected isOverrideNodeSize: boolean = false

    @property(cc.Boolean)
    protected isOverrideNodeScale: boolean = false

    @property(cc.Boolean)
    protected isOverrideFontSize: boolean = false;

    @property(cc.Boolean)
    protected isOverrideAnchor: boolean = false;
    @property(cc.Boolean)
    protected isOverrideLayoutComponet: boolean = false;

    protected CurrentNode: cc.Node[] = [];
    protected NodePostion_Horizontal: cc.Node[] = [];
    protected NodePostion_Vertical: cc.Node[] = [];

    ingoreSizeName: string[] = []
    ingoreNodeName: string[] = [];

    //  
    //Class        

    protected onLoad(): void {
        EventSystem.on(EventType.clientReady, () => {
            this.init()
            this.commonUI_PanleLayoutSetting();//clientReady
        })
    }
    other_commonUI_PanleLayoutSetting: () => void;

    init() {
        this.collectChildNode(this.CollectionTarget, this.CurrentNode)
        this.collectChildNode(this.Horizontal, this.NodePostion_Horizontal)
        this.collectChildNode(this.Vertical, this.NodePostion_Vertical)

        EventSystem.on(EventType.ScreenChange, () => {
            this.commonUI_PanleLayoutSetting();
        })
    }


    collectChildNode(currentNode: cc.Node, NodeCollection: cc.Node[]) {
        let needingore = false
        this.ingoreNodeName.forEach(NodeName => {
            if (currentNode.name == NodeName) needingore = true;
        });
        if (needingore) return;

        for (let i = 0; i < currentNode.children.length; i++) {
            if (currentNode.children.length > 0) {
                NodeCollection.push(currentNode.children[i])
                this.collectChildNode(currentNode.children[i], NodeCollection)
            }
            else return;
        }
    }
    commonUI_PanleLayoutSetting() {

        //
        if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
            for (let i = 0; i < this.NodePostion_Horizontal.length; i++) {
                this.move_CurrentNode_To_TargetNode(this.CurrentNode[i], this.NodePostion_Horizontal[i])
            }

        }
        // 
        else {
            for (let i = 0; i < this.NodePostion_Horizontal.length; i++) {
                this.move_CurrentNode_To_TargetNode(this.CurrentNode[i], this.NodePostion_Vertical[i])
            }
        }

        this.other_commonUI_PanleLayoutSetting();

    }

    move_CurrentNode_To_TargetNode(currentNode: cc.Node, targetNode: cc.Node) {


        if (currentNode.name != targetNode.name)
            console.log(`Warnning!!!!\n
            currentNodeName=${currentNode.parent.name + '/' + currentNode.name}\n
            targetNodeName=${targetNode.parent.name + '/' + targetNode.name}\n
            ----------------------------------`)

        if (currentNode.name == 'RICHTEXT_CHILD') {
            // console.log('RICHTEXT_CHILD', currentNode.parent.name)
            return;
        }

        let needingore = false
        this.ingoreNodeName.forEach(NodeName => {
            if (currentNode.name == NodeName) needingore = true;
        });
        if (needingore) return;

        // cc.Canvas.instance.designResolution = designResolution

        if (currentNode == null) return;
        let OriginalWorldPos = currentNode.convertToWorldSpaceAR(cc.v2(0, 0))
        let target = targetNode.convertToWorldSpaceAR(cc.v2(0, 0))
        let result = cc.v2(target.x - OriginalWorldPos.x, target.y - OriginalWorldPos.y)
        currentNode.x += result.x
        currentNode.y += result.y


        if (this.isOverrideNodeSize) {

            if (this.ingoreSizeName.has(currentNode.name)) return;
            currentNode.setContentSize(targetNode.getContentSize())

        }
        if (this.isOverrideNodeScale) {
            currentNode.setScale(targetNode.getScale(cc.v2()))
        }

        if (this.isOverrideFontSize) {
            if (currentNode.getComponent(cc.Label) != null && targetNode.getComponent(cc.Label) != null) {
                currentNode.getComponent(cc.Label).fontSize = targetNode.getComponent(cc.Label).fontSize;
                currentNode.getComponent(cc.Label).lineHeight = targetNode.getComponent(cc.Label).lineHeight;
            }
        }


        if (this.isOverrideAnchor) {

            currentNode.setAnchorPoint(targetNode.getAnchorPoint())
        }

        if (this.isOverrideLayoutComponet) {

            if (currentNode.getComponent(cc.Layout) != null && targetNode.getComponent(cc.Layout) != null) {

                let currentNode_Layout = currentNode.getComponent(cc.Layout)
                let targetNode_Layout = targetNode.getComponent(cc.Layout)

                currentNode_Layout.spacingX = targetNode_Layout.spacingX
                currentNode_Layout.spacingY = targetNode_Layout.spacingY
                currentNode_Layout.paddingLeft = targetNode_Layout.paddingLeft
                currentNode_Layout.paddingLeft = targetNode_Layout.paddingLeft
                currentNode_Layout.type = targetNode_Layout.type
                currentNode_Layout.resizeMode = targetNode_Layout.resizeMode
                currentNode_Layout.updateLayout();
            }
        }



    }
}