import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";
import CommonUI_ViewController from "./CommonUI_ViewController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FreeGameObjectController extends CommonUI_ViewController {

    @property(cc.Node)
    FreeGameObject_Hori: cc.Node = null;
    @property(cc.Node)
    FreeGameObject_Verti: cc.Node = null;


    start() {
        EventSystem.on(EventType.ScreenChange, () => {
            if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
                this.FreeGameObject_Hori.active = true;
                this.FreeGameObject_Verti.active = false;
            }
            else {

                this.FreeGameObject_Hori.active = false;
                this.FreeGameObject_Verti.active = true;
            }
        })
    }
}
