
// import AxisManager from ".";
import GameServer from "./FakeServer/GameServer";
import FreeAxisManager from "./FreeAxisManager";
import GameManager from "./GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class AxisListManager extends cc.Component {

    // onLoad () {}

    // @property([AxisManager])
    // AxisList: AxisManager[] = [];

    @property([FreeAxisManager])
    FreeAxisList: FreeAxisManager[] = [];

    @property(cc.Node)
    Reels: cc.Node = null;

    @property(cc.Node)
    ReelsBg: cc.Node = null;

    @property(cc.Node)
    EnergyBarBg: cc.Node = null;

    @property(cc.Node)
    ReelHint: cc.Node = null;

    m_ReelsSpining: boolean = false;
    m_ReelsHint: boolean = false;
    m_ReelsHintIndex: number = 0;
    m_ReelsHintAniType: number = 0;
    m_HintTab: boolean[] = [false, false, false, false, false];
    m_Reel_Hint_PosX: number[] = [0, 0, 0, 0, 0];
    m_NowFreeSpinIng: boolean = false;
    m_FreeSpining: boolean = false;

    m_ReelsRectPosition: cc.Vec3[] = [cc.v3(42, 0, 0), cc.v3(0, 183, 0)];
    m_ReelsRectScale: cc.Vec3[] = [cc.v3(0.9, 0.9, 1), cc.v3(0.72, 0.72, 1)];

    start() {

    }

    init() {
        // for (let i = 0; i < this.AxisList.length; i++) {
        //     this.AxisList[i].init(20 + i * 5, i);
        //     this.m_Reel_Hint_PosX[i] = this.AxisList[i].node.x;
        // }

        for (let i = 0; i < this.FreeAxisList.length; i++) {
            this.FreeAxisList[i].init(15 + i * 10, i);
        }
        // this.ReelHint.getComponent(sp.Skeleton).skeletonData = GameManager.AssetsData.reel_hint;
        // this.ReelsBg.getComponent(cc.Sprite).spriteFrame = GameManager.AssetsData.reels_bg;

        // this.EnergyBarBg.getComponent(cc.Sprite).spriteFrame = GameManager.AssetsData.energy_bar;

        this.m_ReelsHintAniType = 0;
        // this.ReelHint.getComponent(sp.Skeleton).setCompleteListener(function() {
        //     GameManager.GamePlay.MainReels.ChangeHintLoop();
        // });

        this.Reels.getChildByName("ReelsRect").active = true;
        this.Reels.getChildByName("FreeReelsRect").active = false;
    }

    // InitFreeReels(scoretab:number[]) {
    //     for(let i = 0; i < this.FreeAxisList.length; i++ ) {
    //         let score =  scoretab[i];
    //         this.FreeAxisList[i].StartInit(score);
    //     }
    // }

    // SetResult(result: number[]) {
    //     for (let i = 0; i < this.AxisList.length; i++) {
    //         let sym_id = [];
    //         for (let j = 0; j < 3; j++) {
    //             let index = i * 3 + j;
    //             sym_id[j] = result[index];
    //         }
    //         this.AxisList[i].SetResult(sym_id);
    //     }
    // }

    SetResultSunScore(scoretab: number[]) {

        return;

        for (let i = 0; i < this.AxisList.length; i++) {
            let score_Tab = [0, 0, 0];
            for (let j = 0; j < 3; j++) {
                let index = i * 3 + j;
                score_Tab[j] = scoretab[index];
            }
            this.AxisList[i].SetResultSunScore(score_Tab);
        }
    }

    SetFreeResult(result: number[]) { //
        for (let i = 0; i < this.FreeAxisList.length; i++) {
            let sym_id = result[i];
            this.FreeAxisList[i].SetResult(sym_id);
        }
    }

    SymbolBoxHide(index: number) {
        // this.SymbolBoxAni_List[index].active = false;
        // if ( this.m_GetSunShowTab[index] ) {
        //     //
        //     if ( !this.SymbolWinAni_List[index].active ) {
        //         this.GetSunShow(index);
        //     }
        // }
    }

    onSpin(freeFlg: boolean) {
        this.m_FreeSpining = freeFlg;
        if (this.m_FreeSpining) {
            let Runningcountindex = 0;
            for (let i = 0; i < this.FreeAxisList.length; i++) {
                if (!this.FreeAxisList[i].m_IsGetSun) {
                    this.FreeAxisList[i].SetRunningcountnum(15 + Runningcountindex * 10);
                    Runningcountindex += 1;
                    // GameManager.GamePlay.SymbolWinShow.SymbolBoxShow(i);
                    this.FreeAxisList[i].AxisRunningStart();
                }
            }
        }
        // else {
        //     for (let i = 0; i < this.AxisList.length; i++) {
        //         this.AxisList[i].AxisRunningStart(this.m_FreeSpining);
        //     }
        // }

        this.m_ReelsSpining = true;
        // GameManager.AssetsData.m_Audio.PlaySound(GameManager.AssetsData.Enum_Sound_Index.Sound_Reel);
    }

    QuickStop() {
        return;
        for (let i = 0; i < this.AxisList.length; i++) {
            this.AxisList[i].QuickStop();
        }
    }

    CheckHint() {
        return;
        let checkindex = this.m_ReelsHintIndex - 1;
        if (this.AxisList[checkindex].m_Spining == false) {
            if (this.ReelHint.active == false) {
                this.HintShowStart();
            }
            if (this.m_ReelsHintIndex >= 5) {
                this.m_ReelsHint = false
                // this.HintShowEnd();
            } else {
                this.ReelHint.x = this.m_Reel_Hint_PosX[this.m_ReelsHintIndex];
                this.m_ReelsHintIndex++;
            }
        }
    }

    HintShowStart() {
        this.m_ReelsHintAniType = 0;
        // this.ReelHint.active = true;
        // // this.ReelHint.getComponent(sp.Skeleton).setAnimation(0, "animation", true);
        // this.ReelHint.getComponent(sp.Skeleton).setAnimation(0, "00_in", false);
        // GameManager.AssetsData.m_Audio.PlaySound(GameManager.AssetsData.Enum_Sound_Index.Sound_HintBox);
    }

    ChangeHintLoop() {
        if (this.m_ReelsHintAniType == 0) {
            this.m_ReelsHintAniType = 1;
            // this.ReelHint.getComponent(sp.Skeleton).setAnimation(0, "01_loop", true);
        }
    }

    HintShowEnd() {
    this.ReelHint.getComponent(sp.Skeleton).clearTracks();
    this.ReelHint.getComponent(sp.Skeleton).setToSetupPose();
    this.ReelHint.active = false;
    // GameManager.AssetsData.m_Audio.StopSound(GameManager.AssetsData.Enum_Sound_Index.Sound_HintBox);
    }

    ChangeSymbol(index: number, id: number) {
        return;
        let Axis_index = Math.floor(index / 3);
        let symbol_index = Math.floor(index % 3) + 1;
        this.AxisList[Axis_index].ChangeSymbol(symbol_index, id);
    }

    RandSymbolID(freeFlg: boolean, index = 0) {
        return;
        let id = this.AxisList[index].RandSymbolID(freeFlg);
        return id;
    }

    SetSymbolShow(index: number, show: boolean) {
        return;
        let Axis_index = Math.floor(index / 3);
        let symbol_index = Math.floor(index % 3) + 1;
        this.AxisList[Axis_index].SetSymbolShow(symbol_index, show);
    }
    SetSymbolDark(index: number, dark: boolean) {
        return;
        let Axis_index = Math.floor(index / 3);
        let symbol_index = Math.floor(index % 3) + 1;
        this.AxisList[Axis_index].SetSymbolDark(symbol_index, dark);
    }

    SetSymbolScore(index: number, score: number) {
        let Axis_index = Math.floor(index / 3);
        let symbol_index = Math.floor(index % 3) + 1;
        this.AxisList[Axis_index].SetSymbolScore(symbol_index, score);
    }

    SetSymbolSunDark(index: number) {
        return;
        let Axis_index = Math.floor(index / 3);
        let symbol_index = Math.floor(index % 3) + 1;
        this.AxisList[Axis_index].SetSymbolSunDark(symbol_index);
    }

    ChangeCameraType(CameraType: number) {
        this.Reels.setPosition(this.m_ReelsRectPosition[CameraType]);
        this.Reels.setScale(this.m_ReelsRectScale[CameraType]);
        if (CameraType == 0) {
            this.ReelsBg.active = true;
            this.EnergyBarBg.active = true;
        } else {
            this.ReelsBg.active = false;
            this.EnergyBarBg.active = false;
        }
    }

    SetFreeSpinIngFlg(flg: boolean) {
        return;
        this.m_NowFreeSpinIng = flg;
        for (let i = 0; i < this.AxisList.length; i++) {
            this.AxisList[i].SetFreeSpinIngFlg(flg);
        }
    }

    ChangeToFreeReels() {
        this.Reels.getChildByName("ReelsRect").active = false;
        this.Reels.getChildByName("FreeReelsRect").active = true;
    }

    ChangeToMainReels() {
        this.Reels.getChildByName("ReelsRect").active = true;
        this.Reels.getChildByName("FreeReelsRect").active = false;
    }

    SetMainReelsShow(show: boolean) {
        this.Reels.getChildByName("ReelsRect").active = show;
    }

    SetFreeReelsShow(show: boolean) {
        this.Reels.getChildByName("FreeReelsRect").active = show;
    }

    update(dt) {
        if (this.m_ReelsSpining) {

            if (this.m_ReelsHint) {
                this.CheckHint();
            }

            if (this.m_FreeSpining) { //FreeGame
                for (let i = 0; i < this.FreeAxisList.length; i++) {
                    if (this.FreeAxisList[i].m_Spining == true) {
                        return;
                    } else {
                        // GameManager.GamePlay.SymbolWinShow.SymbolBoxHide(i);
                        this.SymbolBoxHide(i);
                    }
                }
                //
                for (let i = 0; i < this.FreeAxisList.length; i++) {//
                    this.FreeAxisList[i].AllAxisStopSetting();
                }
            }


            // if( this.m_ReelsHint ) {
            //     this.HintShowEnd();
            // }
            this.m_ReelsSpining = false;


        }
    }
}
