import { AudioManager } from "../Audio/AudioManager";
import { MusicType, SoundType } from "../Audio/AudioType";
import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";
import RemoteAssetManager from "../RemoteAssetManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Game_Introduced extends cc.Component {

    @property([cc.Node])
    PageH: cc.Node[] = [];
    @property([cc.Node])
    PageV: cc.Node[] = [];

    PageIndex: number = 0;

    @property(cc.BlockInputEvents)
    safeBlock: cc.BlockInputEvents = null;

    PageMaxLimit: number = 0;

    startButton: cc.Button = null;
    LButton: cc.Button = null;
    RButton: cc.Button = null;



    protected onLoad(): void {

        // this.node.getChildByName("Start_btn").on('click', () => {
        //     this.node.active = false;
        // })
        this.PageMaxLimit = this.PageH.length;

        this.LButton = this.node.getChildByName('startLbutton').getComponent(cc.Button)
        this.RButton = this.node.getChildByName('startRbutton').getComponent(cc.Button)
        this.startButton = this.node.getChildByName('Start_btn').getComponent(cc.Button)
        this.startButton.node.on('click', () => {
            this.node.active = false;
            AudioManager.playMusic(MusicType.MAINGAME);
            AudioManager.playSound(SoundType.CLICK_BUTTON)
        })

        this.LButton.node.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            if (this.PageIndex - 1 > -1)
                this.PageIndex -= 1
            this.ShowCurrentPage(this.PageIndex);


        })
        this.RButton.node.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            if (this.PageIndex + 1 < (this.PageMaxLimit))
                this.PageIndex += 1
            this.ShowCurrentPage(this.PageIndex);

        })


        EventSystem.on(EventType.AssetLoaded, () => {

            this.init_Spr();
            this.DetectGameLayout();

        })
        // let TapPage = () => {
        //     if (this.PageIndex + 1 < this.PageH.length) {
        //         this.PageIndex += 1
        //         this.updatePage(this.PageIndex);
        //     }
        //     startbuttonHori.active = true
        //     startbuttonVerti.active = true

        // }
        // this.node.getChildByName('hori').on('click', TapPage)
        // this.node.getChildByName('verti').on('click', TapPage)


        // startbuttonVerti.getComponent(cc.Button).node.on('click', () => {
        //     this.node.active = false;
        //     AudioManager.playMusic(MusicType.MAINGAME);
        // })


        EventSystem.on(EventType.ScreenChange, () => {
            this.DetectGameLayout();
        })
    }


    update(dt: number): void {

    }

    DetectGameLayout() {
        if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
            this.node.getChildByName('hori').active = true;
            this.node.getChildByName('verti').active = false;

            this.startButton.node.setPosition(0, -302.6)
            this.LButton.node.setPosition(-518.751, 74.457)
            this.RButton.node.setPosition(525.556, 67.32)
        }
        else {
            this.node.getChildByName('hori').active = false;
            this.node.getChildByName('verti').active = true;

            this.startButton.node.setPosition(0, -(667 / 2))
            this.LButton.node.setPosition(-292, -40.507)
            this.RButton.node.setPosition(300, -40)
        }

    }
    init_Spr() {
        let content = this.node.getChildByName("verti")
        content.getChildByName("1").getComponentInChildren(cc.Sprite).spriteFrame = RemoteAssetManager.self.getSpriteFrame("IntroScreen01_Portrait")
        content.getChildByName("2").getComponentInChildren(cc.Sprite).spriteFrame = RemoteAssetManager.self.getSpriteFrame("IntroScreen02_Portrait")

        content = this.node.getChildByName("hori")
        content.getChildByName("1").getComponentInChildren(cc.Sprite).spriteFrame = RemoteAssetManager.self.getSpriteFrame("IntroScreen01")
        content.getChildByName("2").getComponentInChildren(cc.Sprite).spriteFrame = RemoteAssetManager.self.getSpriteFrame("IntroScreen02")

        this.node.getChildByName('hori').getChildByName('1').active = true;
        this.node.getChildByName('verti').getChildByName('1').active = true;
    }

    ShowCurrentPage(currentPageIndex) {

        console.log(currentPageIndex)
        for (let i = 0; i < this.PageMaxLimit; i++) {
            if (currentPageIndex == i) {
                this.PageH[i].active = true;
                this.PageV[i].active = true;
            }
            else {
                this.PageH[i].active = false;
                this.PageV[i].active = false;
            }
        }

    }

    protected onDisable(): void {
        this.safeBlock.node.active = false;
    }

}
