import { EventSystem, EventType } from "./EventSystem";
import GameManager from "./GameManager";

class AssetEntry {
    key: string = '';
    url: string = '';

    constructor(key: string, url: string) {
        this.key = key;
        this.url = url;
    }
}

class RemoteAssetJSON {
    remoteHost: string = '';
    assets: any = null;
}

const { ccclass, property } = cc._decorator;

@ccclass
export default class RemoteAssetManager extends cc.Component {
    @property(RemoteAssetManager)
    _self: RemoteAssetManager = null;
    static self: RemoteAssetManager = null;

    isLoaded: boolean = false;
    private assetJson: RemoteAssetJSON = null;
    public assetMap: Map<string, any> = new Map<string, any>();
    private spriteAssetMap: Map<string, cc.SpriteFrame> = new Map<string, cc.SpriteFrame>()
    private spineAssetMap: Map<string, sp.SkeletonData> = new Map<string, sp.SkeletonData>()
    public static Symbols: Map<string, cc.SpriteFrame> = new Map();

    configJson: cc.JsonAsset = null;
    DomainName: string = ''
    isAssetLoaded = false;

    protected start(): void {
        this._self = this.node.getComponent(RemoteAssetManager)
        RemoteAssetManager.self = this._self;


        cc.loader.loadRes("remote_assets", cc.JsonAsset, (err, asset: cc.JsonAsset) => {
            this.assetJson = asset.json;
            // Get all asset entries and then load them all
            let assetEntries: AssetEntry[] = [];
            let href = ""

            for (let asset in this.assetJson.assets) {
                let assetEntry = new AssetEntry(asset, this.assetJson.assets[asset]);
                assetEntries.push(assetEntry);
            }

            // if (GameManager.GM.ServerPortocolMgr.offline) { ///rd07
            //     // href = window.location.href;
            //     this.DomainName = this.assetJson.remoteHost
            // }
            if (GameManager.GM.ServerPortocolMgr.checkDev() == true) { //112/rd07
                this.DomainName = this.assetJson.remoteHost
            }
            else {//
                href = window.location.href;
                this.DomainName = href.substring(0, href.lastIndexOf('?') - 1) + '/';
            }
            this.loadRemoteAssets(assetEntries);

        });
    }


    downloadedCount = 0
    TotalCount = 0

    /**
     * 
     * @param DomainName 
     * @param Path 
     * @param Remote_imgName 
     * @param SheetLength 
     * @returns :string[]
     */
    CreatPaths(DomainName: string, Path: string, Remote_imgName: string, SheetLength: number, ext: string = '.png') {


        let ResourcesPaths = []
        for (let count = 0; count < SheetLength; count++) {
            let Quene = '_000'
            if (count > 99) {
                Quene = '_' + count.toString();
            }
            else if (count > 9) {
                Quene = '_0' + count.toString();
            }
            else {
                Quene = '_00' + count.toString();
            }
            ResourcesPaths[count] = DomainName + Path + Remote_imgName + Quene + ext
        }
        // console.log('', Remote_imgName)
        // console.log(fileName, '=>', ResourcesPaths)
        // return ResourcesPaths
        return ResourcesPaths;
    }

    /**
     * 
     * @param Paths 
     * @param Out_SeuqnceImgs ->cc.SpriteFrame[] 
     * @returns 
     */
    private downloadSequenceImgs(Paths: string[], Out_SeuqnceImgs: cc.SpriteFrame[]) {
        return new Promise((resolve, rej) => {
            cc.loader.load(Paths, (completecount, totalcount) => {
                // console.log(completecount)
            }, (err, res) => {
                if (err) console.error(`${Paths}, !Download Failed!`)
                for (const a in res._map) {
                    // console.log(a)
                    if (Object.prototype.hasOwnProperty.call(res._map, a)) {
                        let _sprFrame = null;
                        _sprFrame = new cc.SpriteFrame(res._map[a])
                        Out_SeuqnceImgs.push(_sprFrame)
                    }
                }
                this.downloadedCount += Out_SeuqnceImgs.length
                let completePercent = Math.round((this.downloadedCount / this.TotalCount) * 100) / 100
                GameManager.GM.LoadingPage.ProgressBar.progress = completePercent
                resolve('')
            })
        })
    }

    DownloadProgressCallback() {
        // console.log(completedCount, '/', totalcount)
        // this.downloadedCount += Out_SeuqnceImgs.length
        let completePercent = Math.round((this.resDownloadedCount / this.resTotalCount) * 100) / 100

        GameManager.GM.LoadingPage.ProgressBar.progress = completePercent

        if (this.resDownloadedCount >= this.resTotalCount) {
            // Trigger events
            this.isAssetLoaded = true;
            EventSystem.emit(this.node, EventType.AssetLoaded)
        }
    }

    private creatAnimationClip(SprFrames: cc.SpriteFrame[], _ClipName, _Fps: number = 20, isLoop: boolean = true) {
        // console.log('SprFramesSprFrames', SprFrames)
        let clip = cc.AnimationClip.createWithSpriteFrames(SprFrames, _Fps)
        clip.name = _ClipName
        if (isLoop)
            clip.wrapMode = cc.WrapMode.Loop
        else {
            clip.wrapMode = cc.WrapMode.Default
        }
        // console.log('clipclip', clip)
        return clip;
    }



    //#endregion

    resTotalCount = 0;
    resDownloadedCount = 0;
    private loadRemoteAssets(assetEntries: AssetEntry[]) {
        // Compile a list of URLs to load
        let urlList: string[] = [];

        for (let idx = 0; idx < assetEntries.length; idx++) {
            urlList.push(this.DomainName + assetEntries[idx].url)

        }

        this.resTotalCount = urlList.length

        // this.test();

        // cc.assetManager.loadRemote('http://rd07.zwai.com.tw/Resources/QueenSlot/Spine/scatter.json', (err, res) => {
        //     console.log(res)
        // })
        for (let idx = 0; idx < urlList.length; idx++) {
            cc.assetManager.loadRemote(urlList[idx], (error: any, results: any) => {
                if (error) {
                    cc.error(`Error URL ${urlList[idx]}`)
                    return;
                }
                // let content = results._map[this.DomainName + assetEntries[idx].url]
                this.assetMap.set(assetEntries[idx].key, results)
                // console.log('downloaded-', urlList[idx], idx)
                this.resDownloadedCount += 1
                this.DownloadProgressCallback()
            })

        }


    }


    public getSpriteFrame(fileName: string): cc.SpriteFrame {
        if (this.spriteAssetMap.has(fileName)) {
            return this.spriteAssetMap.get(fileName);
        } else if (this.assetMap.has(fileName)) {
            let sprite = this.getAsset(fileName);
            let frame = new cc.SpriteFrame(sprite);
            this.spriteAssetMap.set(fileName, frame);
            return frame;
        }

        return null;
    }



    public getSpine(fileName: string, DefinetextureName?: string[]): sp.SkeletonData {

        if (this.spineAssetMap.has(fileName)) {
            return this.spineAssetMap.get(fileName)
        }
        else {
            //#region spinePng
            let Textures: cc.Texture2D[] = []
            if (DefinetextureName != undefined) {
                for (let idx = 0; idx < DefinetextureName.length; idx++) {
                    Textures.push(this.getAsset(DefinetextureName[idx] + '.png'));
                }
            }
            else {
                Textures.push(this.getAsset(fileName + '.png'))
            }
            //#endregion

            let alts = this.getAsset(fileName + '.atlas')
            let JsonFile = this.getAsset(fileName + '.json')
            let spineData = new sp.SkeletonData()

            spineData.skeletonJson = JsonFile.json
            spineData.atlasText = alts.text
            spineData.textures = Textures

            let _textureNames = [];
            if (DefinetextureName != undefined) {
                for (let idx = 0; idx < DefinetextureName.length; idx++) {
                    _textureNames.push(DefinetextureName[idx] + '.png');
                }
            }
            else {
                _textureNames = [fileName + '.png']
            }

            spineData.textureNames = _textureNames


            this.spineAssetMap.set(fileName, spineData)
            return spineData
        }

    }
    public getAsset(fileName: string): any {
        return this.assetMap.get(fileName);
    }

}





