  Bet_list: number[] = []
  BetIdx: number = 0;
  GameLine: number = 50;
  UserCredit: number = 1000;
  TotalBet: number = 1;
  ServerResult: [][] = [];
  ServerDataIndex: number = 0;
  ServerData: ServerDataFormat = null;



  private _BuyFreeGamePay: number = 1;
