import { AudioManager } from "./Audio/AudioManager";
import { SoundType } from "./Audio/AudioType";
import { EventSystem, EventType } from "./EventSystem";
import GameManager from "./GameManager";
import { Symbol_ID } from "./Roller";


const { ccclass, property } = cc._decorator;

@ccclass
export default class WildLock extends cc.Component {

    Wilds: sp.Skeleton[] = [];
    WildLockList: boolean[] = [];
    temp_FreeGameLastResult: number[] = [];

    FreeGameWildinit() {
        let WildAxisParent = GameManager.GM.Instance_WheelManager.node.getChildByName('Wilds_Lock')
        let WildAxis = WildAxisParent.children//5
        console.log(WildAxis)

        for (let idx = 0; idx < WildAxis.length; idx++) {
            for (let j = 0; j < WildAxis[idx].children.length; j++) {
                let Axis = WildAxis[idx].children[j].getComponent(sp.Skeleton);
                // if (Axis.node.activeInHierarchy)
                Axis.node.opacity = 0;
                this.Wilds.push(Axis)
            }
        }
        for (let i = 0; i < 20; i++) {
            this.WildLockList.push(false)
        }

    }

    updateFreeGameWildList = (ListIdx: number, symbol_id: number) => {
        let name = ''

        switch (symbol_id) {
            case Symbol_ID.Wild:
                name = WildAnimName.Wild
                break;
            case Symbol_ID.Wildx2:
                name = WildAnimName.Wildx2
                break;
            case Symbol_ID.Wildx3:
                name = WildAnimName.Wildx3
                break;
            // case 13:
            //     name = WildAnimName.Wildx5
            // break;
        }
        if (name != '') {
            // this.Wilds[ListIdx].node.active = true;
            if (this.Wilds[ListIdx].node.opacity != 255) {
                this.Wilds[ListIdx].setAnimation(0, name, false)
                AudioManager.playSound(SoundType.WildStick)
            }
            this.Wilds[ListIdx].node.opacity = 255
            this.WildLockList[ListIdx] = true;
        }
    }
    resetFreeGameWildList = () => {

        //idx_roll 0~4 ==5
        //ColumnData 0~5 ==6
        // 1~4 
        // console.log(this.temp_FreeGameLastResult)

        GameManager.GM.Instance_WheelManager.Rollers.forEach(roller => {
            console.log(roller.ColumnData)
        })

        for (let idx_roll = 0; idx_roll < 5; idx_roll++) {
            for (let symbol_position = 1; symbol_position < 5; symbol_position++) {
                GameManager.GM.Instance_WheelManager.Rollers[idx_roll].ColumnData[symbol_position] = (this.temp_FreeGameLastResult.shift() - 1);//offset -1

            }
        }

        /* GameManager.GM.Instance_WheelManager.Rollers[0].ColumnData[1] = this.temp_FreeGameLastResult[0]
        // GameManager.GM.Instance_WheelManager.Rollers[0].ColumnData[2] = this.temp_FreeGameLastResult[1]
        // GameManager.GM.Instance_WheelManager.Rollers[0].ColumnData[3] = this.temp_FreeGameLastResult[2]
        // GameManager.GM.Instance_WheelManager.Rollers[0].ColumnData[4] = this.temp_FreeGameLastResult[3]

        // GameManager.GM.Instance_WheelManager.Rollers[1].ColumnData[1] = this.temp_FreeGameLastResult[4]
        // GameManager.GM.Instance_WheelManager.Rollers[1].ColumnData[2] = this.temp_FreeGameLastResult[5]
        // GameManager.GM.Instance_WheelManager.Rollers[1].ColumnData[3] = this.temp_FreeGameLastResult[6]
        // GameManager.GM.Instance_WheelManager.Rollers[1].ColumnData[4] = this.temp_FreeGameLastResult[7]

        // GameManager.GM.Instance_WheelManager.Rollers[2].ColumnData[1] = this.temp_FreeGameLastResult[8]
        // GameManager.GM.Instance_WheelManager.Rollers[2].ColumnData[2] = this.temp_FreeGameLastResult[9]
        // GameManager.GM.Instance_WheelManager.Rollers[2].ColumnData[3] = this.temp_FreeGameLastResult[10]
        // GameManager.GM.Instance_WheelManager.Rollers[2].ColumnData[4] = this.temp_FreeGameLastResult[11]

        // GameManager.GM.Instance_WheelManager.Rollers[3].ColumnData[1] = this.temp_FreeGameLastResult[12]
        // GameManager.GM.Instance_WheelManager.Rollers[3].ColumnData[2] = this.temp_FreeGameLastResult[13]
        // GameManager.GM.Instance_WheelManager.Rollers[3].ColumnData[3] = this.temp_FreeGameLastResult[14]
        // GameManager.GM.Instance_WheelManager.Rollers[3].ColumnData[4] = this.temp_FreeGameLastResult[15]

        // GameManager.GM.Instance_WheelManager.Rollers[4].ColumnData[1] = this.temp_FreeGameLastResult[16]
        // GameManager.GM.Instance_WheelManager.Rollers[4].ColumnData[2] = this.temp_FreeGameLastResult[17]
        // GameManager.GM.Instance_WheelManager.Rollers[4].ColumnData[3] = this.temp_FreeGameLastResult[18]
        GameManager.GM.Instance_WheelManager.Rollers[4].ColumnData[4] = this.temp_FreeGameLastResult[19] */


        GameManager.GM.Instance_WheelManager.Rollers.forEach(roller => {
            roller.updateSymbols_FreeGamelastResult();
        })

        this.Wilds.forEach(wild => {
            wild.node.opacity = 0;
        })
        for (let i = 0; i < 20; i++) {
            this.WildLockList[i] = false;
        }

    }

}
export enum WildAnimName {
    Wild = 'Win',
    Wildx2 = 'Win_x2',
    Wildx3 = 'Win_x3',
}
