import { EventSystem, EventType } from "../EventSystem";
import GameManager, { GameScreenLayout } from "../GameManager";
import CommonUI_ViewController from "./CommonUI_ViewController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class BigwinCutScene_ViewController extends CommonUI_ViewController {

    @property(cc.Node)
    score: cc.Node = null;
    @property(cc.Node)
    score_hori: cc.Node = null;
    @property(cc.Node)
    score_verti: cc.Node = null;

    start() {
        this.ingoreNodeName = ['score']
    }

    other_commonUI_PanleLayoutSetting = () => {


        if (GameManager.GM.currentGameScreenLayout == GameScreenLayout.Horizontal) {
            let pos = this.score_hori.getPosition()
            this.score.setPosition(pos.x, pos.y, 0)
        }
        else {
            let pos = this.score_verti.getPosition()
            this.score.setPosition(pos.x, pos.y, 0)
        }
    }


}
