    //#region FreeGame FreeGameTimes
    private _FreeGameTimes: number = 0;
    public get FreeGameTimes(): number {
        return this._FreeGameTimes;
    }
    public set FreeGameTimes(v: number) {
        this._FreeGameTimes = v
        this.FreeGameleftTime.getChildByName('times').getComponent(cc.Label).string = v.toString();
    }

    //#endregion




    private _GameState: string = 'Loading';
    public get GameState(): string {
        return this._GameState;
    }
    public set GameState(v: string) {
        console.log(`%c[${this._GameState}]  --> [%c${v}]`,
            'color:#FF4D00; font-weight:bold;', "color:#FF4D00;");
        // console.groupCollapsed();
        this._GameState = v;
    }

