    BackToNormalCheckMystery() {
        if (this.hasFreeGameFlag || this.BonusFlag) {
            this.scheduleOnce(() => {

                EventSystem.emit(this.node, EventType.BackToNormal)
            }, 0.1)
        }
        else {
            EventSystem.emit(this.node, EventType.hasMystery)
        }
    }


    FreeSpin() {
        // this.GoldFrame.setAnimation(0, 'goldbox', false)
        this.UI_BuyFreeGame.Eanble_BuyFreeSpinBtn(false)
        // this.FreeGameBG.setAnimation(0, BackgroundSpineEnum.Bonus_Idle, true)
        if (this.FreeGameTimes - 1 > -1) {
            this.FreeGameTimes -= 1;
            let lbl_freetimeleft = this.userUI.top_infobar.getChildByName('lbl_FreeSpinLeft').getComponent(cc.Label)
            lbl_freetimeleft.string = `FREE SPIN LEFT ${this.FreeGameTimes.toString()}`
            GameManager.GM.hasResult = true;
            AudioManager.playSound(SoundType.SPIN_START)
            this.isRollerStoped = false
            this.saveGameResult = [];
            // GameManager.GM.FreeGameBG.setAnimation(0, BackgroundSpineEnum.Bonus_Idle, true);

            GameManager.ResetAllSymbolAction();
            this.userUI.btn_Stop.node.active = true;
            this.Spin.node.active = false;//
            // EventSystem.emit(this.node, EventType.Running)//
            this.GameState = EventType.Running;
            GameServer.self.ServerDataIndex += 1;
            let speed = 0.016666 * 2

            // if (this.userUI.QuickSpinFlag) {
            // this.scheduleOnce(() => {
            for (let i = 0; i < 5; i++) {//Roller
                this.Instance_WheelManager.Rollers[i].
                    TurnWheel(this.Instance_WheelManager.Rollers[i].node, speed, i)
            }
            // }, 0.15)
            // }
            // else {
            //     for (let i = 0; i < 5; i++) {//Roller
            //         // this.scheduleOnce(() => {
            //         this.Instance_WheelManager.Rollers[i].
            //             TurnWheel(this.Instance_WheelManager.Rollers[i].node, speed, i)
            //         // }, 0.1 * (i + 1))
            //     }
            // }
            // this.scheduleOnce(() => {
            //     // AudioManager.playSound(SoundType.REEL_SPINNING, true)
            // }, 0.3)

            // AudioManager.playSound(SoundType.TOPROW_MOVE)
            cc.log(`${this.FreeGameTimes}`)
        }
        else {
            EventSystem.emit(this.node, EventType.FreeGameEnd)
        }
    }



