    DetectScreenSize() {
        let result = ''
        // console.log('DetectScreenSize')
        // if (cc.view.getCanvasSize().width > cc.view.getCanvasSize().height)
        //     result = GameScreenLayout.Horizontal;
        // else
        result = GameScreenLayout.Vertical;

        if (result != this.currentGameScreenLayout)
            this.ChangeScreenSize(result)
    }

    ChangeScreenSize(layout) {
        if (layout == GameScreenLayout.Horizontal) {
            cc.Canvas.instance.designResolution = new cc.Size(1334, 750)
            cc.Canvas.instance.fitWidth = true;
            cc.Canvas.instance.fitHeight = true;

            cc.find('Canvas/Mask').setContentSize(1334, 750)

            this.currentGameScreenLayout = layout;
            EventSystem.emit(this.node, EventType.ScreenChange, this.currentGameScreenLayout)
            // this.GoldFrame.node.scale = 1.2

            //BG
            this.MainGameBG.skeletonData = this.GameMGBG_spines[0]
            this.FreeGameBG.skeletonData = this.GameFGBG_spines[0]
            this.set_BG(this.currentBG)
            // this.MainGameBG.setAnimation(0, this.currentBG_SpineState, true)
            // console.log('currentBG_SpineState', this.currentBG_SpineState)
            this.Effect_FreeGameCutScene.node.rotation = 0;
            // //Bonus
            // this.Instance_BonusManager.node.scale = 1;
            this.WinScoreBG_Hori.node.opacity = 255;
            this.WinScoreBG_Verti.node.opacity = 0;

        }
        else if (layout == GameScreenLayout.Vertical) {
            this.Effect_FreeGameCutScene.node.rotation = 90;
            cc.Canvas.instance.designResolution = new cc.Size(750, 1334)
            cc.find('Canvas/Mask').setContentSize(750, 1334)
            // this.GoldFrame.node.scale = 0.9
            cc.Canvas.instance.fitWidth = true;
            cc.Canvas.instance.fitHeight = true;

            this.currentGameScreenLayout = layout;
            EventSystem.emit(this.node, EventType.ScreenChange, this.currentGameScreenLayout)

            //BG
            this.MainGameBG.skeletonData = this.GameMGBG_spines[1]
            this.FreeGameBG.skeletonData = this.GameFGBG_spines[1]
            this.set_BG(this.currentBG)
            // this.MainGameBG.setAnimation(0, this.currentBG_SpineState, true)
            // //Bonus
            // this.Instance_BonusManager.node.scale = 0.75;
            this.WinScoreBG_Hori.node.opacity = 0;
            this.WinScoreBG_Verti.node.opacity = 255;
        }
        else {
            this.DetectScreenSize();
        }

    }

    lateUpdate() {
        // console.log('GameScreenLayout', this.currentGameScreenLayout)
        if (this.isGameReady) {
            this.DetectScreenSize();
            this.Credit.string = `${UI_interface.self.currency} ${GameServer.self.UserCredit.toPrecisionStr()}`
        }
    }



    static toStringThousandth(value) {
        if (value == undefined)
            console.trace(value)

        if (value.toPrecisionStr instanceof Function) {
            if (GameServer.self.getTotalBet() >= 100)
                return value.toPrecisionStr(0)
            else {
                return value.toPrecisionStr(2)
            }
        }
        else {
            return value.toString()
        }

    }



    ScatterReward() {

        GameManager.ResetAllSymbolAction();
        let MainResult_symbolList = GameServer.self.ServerResult[0]["betNumber"]
        MainResult_symbolList.forEach((symbol_id, idx) => {
            if (symbol_id == Symbol_ID.Scatter + 1) {
                this.Instance_WheelManager.symbol_spineComponet_FormatToServerResult[idx].node.active = true;
                this.Instance_WheelManager.symbol_spineComponet_FormatToServerResult[idx].node.opacity = 255;
                this.Instance_WheelManager.symbol_spineComponet_FormatToServerResult[idx].skeletonData = this.Instance_WheelManager.SpineDatas[Symbol_ID.Scatter];
                this.Instance_WheelManager.symbol_spineComponet_FormatToServerResult[idx].setAnimation(0, 'Win', false)
            }
        });
    }

