import { AudioManager } from "./Audio/AudioManager";
import { SoundType } from "./Audio/AudioType";
import { EventSystem, EventType } from "./EventSystem";
import GameServer from "./FakeServer/GameServer";
import GameManager, { GameScreenLayout } from "./GameManager";


const { ccclass, property } = cc._decorator;

@ccclass
export default class Buy_FreeSpin extends cc.Component {

    Price: cc.Label = null;
    Btn_openConfirmPage: cc.Button = null;
    BuyFreeGameCheckPage: cc.Node = null;
    btn_confirm: cc.Button = null;
    btn_cancel: cc.Button = null;



    start() {
        this.BuyFreeGameCheckPage = cc.find('Canvas/Mask/BuyFreeGamePage')

        this.btn_cancel = this.BuyFreeGameCheckPage.getChildByName('btn_cancel').getComponent(cc.Button)
        this.btn_confirm = this.BuyFreeGameCheckPage.getChildByName('btn_confirm').getComponent(cc.Button)
        this.Btn_openConfirmPage = this.node.getComponent(cc.Button)

        this.btn_cancel.node.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            this.BuyFreeGameCheckPage.active = false;
        })
        this.btn_confirm.node.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            if (GameManager.GM.ServerPortocolMgr.offline) {
                this.BuyFreeGameCheckPage.active = false;
                GameManager.GM.userUI.btn_Stop.node.active = true;
                GameManager.GM.userUI.btn_Auto.node.active = false;
                GameManager.GM.userUI.btn_Bet.node.active = false;
                GameManager.GM.Spin.node.active = false;
                GameServer.self.SetResult(GameManager.GM.ServerPortocolMgr.FreeGameDemo);
                GameManager.GM.onSpin();
            }
            else {
                if (GameServer.self.UserCredit >= GameServer.self.getTotalBet() * GameServer.self.BuyFreeGamePay) {
                    GameManager.GM.BuyFreeGameFlag = true;
                    this.BuyFreeGameCheckPage.active = false;
                    GameManager.GM.userUI.btn_Stop.node.active = true;
                    GameManager.GM.userUI.btn_Auto.node.active = false;
                    GameManager.GM.userUI.btn_Bet.node.active = false;
                    GameManager.GM.Spin.node.active = false;
                    GameManager.GM.ServerPortocolMgr.send( //SERVER
                        'SCheckCanBet', { setvalue: "1" }
                    )
                    GameManager.GM.onSpin();
                }
                else {
                    GameManager.GM.Msg_notEnoughBalance(GameManager.GM.userUI.language_assets.InsufficientCredit)
                }
            }

        })

        this.Price = this.node.getComponentInChildren(cc.Label)
        // this.update_BuyFreeSpinPrice();
        EventSystem.on(EventType.ScreenChange, this.LayoutChange)

        this.Btn_openConfirmPage.node.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            this.BuyFreeGameCheckPage.active = true;

        })

    }
    LayoutChange = (layout: GameScreenLayout) => {
        if (layout == GameScreenLayout.Horizontal) {
            this.node.setPosition(-584, -112)
            this.node.scale = 1
            GameManager.GM.FreeGameleftTime.setPosition(-584, -112)
            GameManager.GM.FreeGameleftTime.scale = 1

        }
        //169/227
        else {
            let scale = 0.7;
            let width = (-750 / 2) + ((169 * scale) / 2)
            let height = (1334 / 2) - ((227 * scale) / 2) - 70

            this.node.setPosition(width, height)
            this.node.scale = scale
            GameManager.GM.FreeGameleftTime.setPosition(width, height)
            GameManager.GM.FreeGameleftTime.scale = scale
        }

    }

    Eanble_BuyFreeSpinBtn(bool: boolean) {
        this.Btn_openConfirmPage.interactable = bool
    }

    update_BuyFreeSpinPrice() {
        this.Price.string = `${GameManager.GM.userUI.currency} ${(GameServer.self.getTotalBet() * GameServer.self.BuyFreeGamePay).toLocaleString()}`
    }





    // update (dt) {}
}
