import { AudioManager } from "./Audio/AudioManager";
import { SoundType } from "./Audio/AudioType";
import { EventSystem, EventType } from "./EventSystem";
import GameServer from "./FakeServer/GameServer";
import GameManager, { BackgroundSpineEnum, GameScreenLayout } from "./GameManager";
import RemoteAssetManager from "./RemoteAssetManager";
import UI_interface from "./UI/UI_interface";
import WheelManager from "./WheelManager";


const { ccclass, property } = cc._decorator;

export enum RollerName {
    axis1 = 0,
    axis2 = 1,
    axis3 = 2,
    axis4 = 3,
    axis5 = 4,
}

export enum Symbol_ID {

    High1 = 0,
    High2 = 1,
    High3 = 2,
    High4 = 3,
    Low1 = 4,
    Low2 = 5,
    Low3 = 6,
    Low4 = 7,
    Low5 = 8,
    Wild = 9,
    Wildx2 = 10,
    Wildx3 = 11,
    Wildx5 = 12,
    Scatter = 13
}

@ccclass
export default class Roller extends cc.Component {

    SymbolMaxTypeCount = 14;
    Symbol_column_count: number = 6;//Symbol
    Symbol_migration_distance: number = 141.75; // 567/4
    Column_Symbols: cc.Node[] = [];
    ColumnData: number[] = []; // 
    m_Sprite: cc.SpriteFrame[] = [];
    mblur_Sprite: cc.SpriteFrame[] = [];
    m_stoped: boolean = false; //
    myName = -1
    m_rollTempoUnit = 4//

    onLoad() {
        this.node.children.forEach((childNode) => {
            this.Column_Symbols.push(childNode);
        });
        EventSystem.on(EventType.AssetLoaded, () => {
            this.init();
        })
    }

    init() {
        // this.myName = this.node.name;
        for (let i = 0; i < this.SymbolMaxTypeCount; i++) {
            //Roller Symbol
            let texture2D = RemoteAssetManager.self.getAsset(`symbol_${i}`)
            let img: cc.SpriteFrame = new cc.SpriteFrame(texture2D)
            img.name = `symbol_${i}`
            this.m_Sprite.push(img)
        }
        for (let i = 0; i < this.SymbolMaxTypeCount; i++) {
            //Roller Symbol
            let texture2D = RemoteAssetManager.self.getAsset(`blursymbol_${i}`)
            let img: cc.SpriteFrame = new cc.SpriteFrame(texture2D)
            img.name = `blursymbol_${i}`
            this.mblur_Sprite.push(img)
        }

        this.ColumnData = this.firstGameResult(); //

        //
        //SymboleNode  
        GameManager.GM.count_Rollerinit += 1;
    }


    checkScatter = (ResultTable) => {
        let _listenFreeGame: boolean[] = [];
        let ScaterCount = 0;
        for (let i = 0; i < 5; i++) {
            if (ScaterCount >= 2) {
                _listenFreeGame.push(true)
            }
            else {
                _listenFreeGame.push(false)
            }
            for (let j = (i * 4); j < (4 * (i + 1)); j++) {
                if (ResultTable[j] == Symbol_ID.Scatter) {
                    ScaterCount += 1;
                    break;
                }
            }
        }
        return _listenFreeGame
    }

    check_Listen_roll_time = (ListenArray: boolean[]) => {
        let MultiTime = 1;
        let ListenLength = [];
        for (let i = 0; i < 5; i++) {
            if (ListenArray[i])
                MultiTime += 1

            ListenLength.push(MultiTime)
        }
        return ListenLength


    }

    async TurnWheel(targetNode: cc.Node, TurnSpeed: number, rollerIdx) {
        this.myName = rollerIdx;
        let RollRound = 4;

        if (GameManager.GM.userUI.QuickSpinFlag && !GameManager.GM.isFreeGameState) this.m_stoped = true;
        await this.DropUp(targetNode) //


        for (let times = 0; times < RollRound * this.m_rollTempoUnit; times++) {
            if (this.m_stoped) break;
            await this.DropDown(targetNode, TurnSpeed, -1, true);
        }

        for (; !GameManager.GM.hasResult;) {
            await this.DropDown(targetNode, TurnSpeed);
        }



        let Table: number[] = [];
        let ServerResult: number[] = GameServer.self.ServerResult[GameServer.self.ServerDataIndex].betNumber;
        let copyResult = Object.assign([], ServerResult)
        GameManager.GM.Instance_WheelManager.FG_WildLock.temp_FreeGameLastResult = copyResult;


        for (let i = 0; i < ServerResult.length; i++) { //this round Result symbols
            let num = ServerResult[i] - 1;
            Table.push(num)
        }

        for (let i = 0; i < Table.length; i++) {
            if (GameManager.GM.Instance_WheelManager.FG_WildLock.WildLockList[i]) {
                Table[i] = this.Random(7, 0)
            }
        }

        let ListenFreeGame = this.checkScatter(Table);
        let ListenLength: number[] = this.check_Listen_roll_time(ListenFreeGame)



        switch (this.myName) {
            case RollerName.axis1:
                // GameManager.GM.showRecordTime("")
                // for (let times = 0; times < 2; times++) {
                //     if (this.m_stoped) break;
                //     await this.DropDown(targetNode, TurnSpeed);
                // }

                for (let i = 3; i >= 0; i--) {
                    // if (this.m_stoped) {
                    // this.DropDown(targetNode, TurnSpeed, Table[i] - 1);
                    // }
                    // else {
                    await this.DropDown(targetNode, TurnSpeed, Table[i]);
                    // }
                }

                // if (this.m_stoped) {
                //     this.DropDown(targetNode, TurnSpeed).then(() => {
                //         AudioManager.playSound(SoundType.SPIN_STOP)
                //         let _symbol_ID: number[] = [];//symbol
                //         let _ListIdx: number[] = [];//
                //         for (let i = 0; i <= 3; i++) {
                //             _symbol_ID.push(Table[i])
                //             _ListIdx.push(i)
                //         }
                //         this.DampUp(targetNode, _ListIdx, _symbol_ID)
                //     });
                // }
                // else {
                // }
                let axis1_symbol_ID: number[] = [];//symbol
                let axis1_ListIdx: number[] = [];//
                await this.DropDown(targetNode, TurnSpeed).then(() => {
                    AudioManager.playSound(SoundType.SPIN_STOP)
                    for (let i = 0; i <= 3; i++) {
                        axis1_symbol_ID.push(Table[i])
                        axis1_ListIdx.push(i)
                    }

                });
                // if (this.m_stoped == false)
                await this.DampUp(targetNode)
                this.rollerStopedCheck(axis1_ListIdx, axis1_symbol_ID)



                break;
            case RollerName.axis2:
                console.log("axis2")

                for (let times = 0; times < 2; times++) {
                    if (this.m_stoped) break;
                    await this.DropDown(targetNode, TurnSpeed, -1, true);
                }

                for (let i = 7; i >= 4; i--) {

                    await this.DropDown(targetNode, TurnSpeed, Table[i]);

                }
                let axis2_symbol_ID: number[] = [];//symbol
                let axis2_ListIdx: number[] = [];//

                await this.DropDown(targetNode, TurnSpeed).then(() => {
                    AudioManager.playSound(SoundType.SPIN_STOP)

                    for (let i = 4; i <= 7; i++) {
                        axis2_symbol_ID.push(Table[i])
                        axis2_ListIdx.push(i)
                    }
                    if (ListenFreeGame[2]) {// Roll 0~4
                        GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[2].node.active = true;
                        GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[2].setAnimation(0, 'animation', true);
                        AudioManager.playSound(SoundType.ScatterListent, true);
                    }
                });
                // if (this.m_stoped == false)
                await this.DampUp(targetNode)
                this.rollerStopedCheck(axis2_ListIdx, axis2_symbol_ID)
                break;

            case RollerName.axis3:
                console.log("axis3")
                for (let times = 0; times < 4; times++) {
                    if (this.m_stoped) break;
                    await this.DropDown(targetNode, TurnSpeed, -1, true);
                }


                if (ListenFreeGame[2]) {
                    for (let times = 0; times < this.m_rollTempoUnit * 5 * ListenLength[2]; times++) {
                        if (this.m_stoped) break;
                        await this.DropDown(targetNode, TurnSpeed, -1, true);
                    }
                }

                for (let i = 11; i >= 8; i--) {
                    await this.DropDown(targetNode, TurnSpeed, Table[i]);
                }


                let axis3_symbol_ID: number[] = [];//symbol
                let axis3_ListIdx: number[] = [];//
                await this.DropDown(targetNode, TurnSpeed).then(() => {
                    AudioManager.playSound(SoundType.SPIN_STOP)
                    for (let i = 8; i <= 11; i++) {
                        axis3_symbol_ID.push(Table[i])
                        axis3_ListIdx.push(i)
                    }
                    if (ListenFreeGame[3]) {// Roll 0~4
                        GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[3].node.active = true;
                        GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[3].setAnimation(0, 'animation', true)
                        AudioManager.playSound(SoundType.ScatterListent, true);
                    }
                    GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[2].node.active = false;
                });
                // if (this.m_stoped == false)
                await this.DampUp(targetNode)
                this.rollerStopedCheck(axis3_ListIdx, axis3_symbol_ID)

                break;

            case RollerName.axis4:
                console.log("axis4")
                for (let times = 0; times < 6; times++) {
                    if (this.m_stoped) break;
                    await this.DropDown(targetNode, TurnSpeed, -1, true);
                }

                if (ListenFreeGame[3]) {
                    for (let times = 0; times < this.m_rollTempoUnit * 5 * ListenLength[3]; times++) {
                        if (this.m_stoped) break;
                        await this.DropDown(targetNode, TurnSpeed, -1, true);
                    }
                }

                for (let i = 15; i >= 12; i--) {
                    await this.DropDown(targetNode, TurnSpeed, Table[i]);

                }

                let axis4_symbol_ID: number[] = [];//symbol
                let axis4_ListIdx: number[] = [];//
                await this.DropDown(targetNode, TurnSpeed).then(() => {
                    AudioManager.playSound(SoundType.SPIN_STOP)

                    for (let i = 12; i <= 15; i++) {
                        axis4_symbol_ID.push(Table[i])
                        axis4_ListIdx.push(i)
                    }
                    // this.DampUp(targetNode, _ListIdx, _symbol_ID)
                    if (ListenFreeGame[4]) {// Roll 0~4
                        GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[4].node.active = true;
                        GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[4].setAnimation(0, 'animation', true)
                        AudioManager.playSound(SoundType.ScatterListent, true);
                    }
                    GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[2].node.active = false;
                    GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[3].node.active = false;
                });
                // if (this.m_stoped == false)
                await this.DampUp(targetNode)
                this.rollerStopedCheck(axis4_ListIdx, axis4_symbol_ID)

                break;

            case RollerName.axis5:
                //TODO
                console.log("axis5")
                for (let times = 0; times < 8; times++) {
                    if (this.m_stoped) break;
                    await this.DropDown(targetNode, TurnSpeed, -1, true);
                }

                if (ListenFreeGame[4]) {
                    for (let times = 0; times < this.m_rollTempoUnit * 5 * ListenLength[4]; times++) {
                        if (this.m_stoped) break;
                        await this.DropDown(targetNode, TurnSpeed, -1, true);
                    }
                }

                for (let i = 19; i >= 16; i--) {
                    await this.DropDown(targetNode, TurnSpeed, Table[i]);

                }
                let axis5_symbol_ID: number[] = [];//symbol
                let axis5_ListIdx: number[] = [];//
                await this.DropDown(targetNode, TurnSpeed).then(() => {
                    AudioManager.playSound(SoundType.SPIN_STOP)
                    GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[2].node.active = false;
                    GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[3].node.active = false;
                    GameManager.GM.Instance_WheelManager.ListenFreeGameEffect[4].node.active = false;

                    for (let i = 16; i <= 19; i++) {
                        axis5_symbol_ID.push(Table[i])
                        axis5_ListIdx.push(i)
                    }

                });
                // if (this.m_stoped == false)
                await this.DampUp(targetNode)
                this.rollerStopedCheck(axis5_ListIdx, axis5_symbol_ID)

                break;

        }
    }


    DropUp(targetNode) {
        let Orignalposition = targetNode.position;
        return new Promise((resolve, reject) => {
            cc.tween(targetNode)
                .by(0.1, { position: cc.v3(0, 20, 0) })//NodeSymbole
                .delay(0.05)
                .to(0.05, { position: Orignalposition })
                .call(resolve)
                .start()
        });
    }

    DropDown(targetNode: cc.Node, speed: number, result: number = -1, _blur: boolean = false) {
        let Orignalposition = targetNode.position;
        let fps = 60
        let dumpoffset = fps * (1 - 0.2)
        let offset = targetNode.y - (((this.Symbol_migration_distance) / fps) * dumpoffset)

        return new Promise((resolve, reject) => {
            cc.tween(targetNode)
                .to(speed, { position: cc.v3(Orignalposition.x, offset, 0) })//NodeSymbole
                .to(0, { position: Orignalposition })

                .call(() => {
                    if (result != -1)
                        this.ChangeSymbol(result);
                    else
                        this.ChangeSymbol(-1, _blur)
                    resolve('')
                })

                .start()
        });
    }



    async DampUp(targetNode,) {

        let frame = 0.0167
        let moveDown_time = (2 * frame)
        let moveUp_time = (6 * frame)
        let offset = (-this.Symbol_migration_distance * 0.10)
        let Orignalposition = targetNode.position;

        return new Promise((resolve, reject) => {
            cc.tween(targetNode)
                .by(moveDown_time, { position: cc.v3(0, offset, 0) })//NodeSymbole
                .to(moveUp_time, { position: Orignalposition })
                .call(() => {
                    resolve('')
                }).start()

        })
    }

    rollerStopedCheck(ListIndex: number[] = [], symbol_ID: number[] = []) {

        if (GameManager.GM.isFreeGameState) {
            for (let i = 0; i < ListIndex.length; i++) {
                GameManager.GM.Instance_WheelManager.FG_WildLock.updateFreeGameWildList(ListIndex[i], symbol_ID[i])
            }
        }
        if (symbol_ID.includes(Symbol_ID.Scatter)) {
            GameManager.GM.Instance_WheelManager.ScatterCollectCount += 1
            let pos = symbol_ID.findIndex((ID) => { return ID == Symbol_ID.Scatter; })
            if (pos > -1) {

                switch (this.myName) {
                    case RollerName.axis1:
                        GameManager.GM.Instance_WheelManager.anim_rollers[0].children[pos + 1].active = true;
                        GameManager.GM.Instance_WheelManager.anim_rollers[0].children[pos + 1].getComponent(sp.Skeleton).skeletonData = GameManager.GM.Instance_WheelManager.SpineDatas[13]
                        GameManager.GM.Instance_WheelManager.anim_rollers[0].children[pos + 1].getComponent(sp.Skeleton).setAnimation(0, BackgroundSpineEnum.Symbol_Win, false)
                        break;

                    case RollerName.axis2:
                        GameManager.GM.Instance_WheelManager.anim_rollers[1].children[pos + 1].active = true;
                        GameManager.GM.Instance_WheelManager.anim_rollers[1].children[pos + 1].getComponent(sp.Skeleton).skeletonData = GameManager.GM.Instance_WheelManager.SpineDatas[13]
                        GameManager.GM.Instance_WheelManager.anim_rollers[1].children[pos + 1].getComponent(sp.Skeleton).setAnimation(0, BackgroundSpineEnum.Symbol_Win, false)
                        break;

                    case RollerName.axis3:
                        GameManager.GM.Instance_WheelManager.anim_rollers[2].children[pos + 1].active = true;
                        GameManager.GM.Instance_WheelManager.anim_rollers[2].children[pos + 1].getComponent(sp.Skeleton).skeletonData = GameManager.GM.Instance_WheelManager.SpineDatas[13]
                        GameManager.GM.Instance_WheelManager.anim_rollers[2].children[pos + 1].getComponent(sp.Skeleton).setAnimation(0, BackgroundSpineEnum.Symbol_Win, false)
                        break;

                    case RollerName.axis4:
                        GameManager.GM.Instance_WheelManager.anim_rollers[3].children[pos + 1].active = true;
                        GameManager.GM.Instance_WheelManager.anim_rollers[3].children[pos + 1].getComponent(sp.Skeleton).skeletonData = GameManager.GM.Instance_WheelManager.SpineDatas[13]
                        GameManager.GM.Instance_WheelManager.anim_rollers[3].children[pos + 1].getComponent(sp.Skeleton).setAnimation(0, BackgroundSpineEnum.Symbol_Win, false)
                        break;

                    case RollerName.axis5:
                        GameManager.GM.Instance_WheelManager.anim_rollers[4].children[pos + 1].active = true;
                        GameManager.GM.Instance_WheelManager.anim_rollers[4].children[pos + 1].getComponent(sp.Skeleton).skeletonData = GameManager.GM.Instance_WheelManager.SpineDatas[13]
                        GameManager.GM.Instance_WheelManager.anim_rollers[4].children[pos + 1].getComponent(sp.Skeleton).setAnimation(0, BackgroundSpineEnum.Symbol_Win, false)
                        break;

                }
            }

            // GameManager.GM.Instance_WheelManager.anim_rollers[]
            switch (GameManager.GM.Instance_WheelManager.ScatterCollectCount) {

                case 1:
                    AudioManager.playSound(SoundType.Scatter01)
                    break;
                case 2:
                    AudioManager.playSound(SoundType.Scatter02)
                    break;
                case 3:
                    AudioManager.playSound(SoundType.Scatter03)
                    break;
                case 4:
                    AudioManager.playSound(SoundType.Scatter04)
                    break;
                case 5:
                    AudioManager.playSound(SoundType.Scatter05)
                    break;
            }

        }

        if (this.myName == RollerName.axis5) {
            GameManager.GM.Instance_WheelManager.dropBookCount = 0;
            GameManager.GM.isRollerStoped = true
            AudioManager.stopSound(SoundType.ScatterListent)
            GameManager.GM.Instance_WheelManager.ScatterCollectCount = 0;
            EventSystem.emit(this.node, EventType.Stoped)
            // GameManager.GM.showRecordTime("")
            GameManager.GM.RecordFlag = false
            GameManager.GM.RecordTime = 0
            GameManager.GM.Instance_WheelManager.resetStopPara();
            // AudioManager.stopSound(SoundType.REEL_SPINNING)
        }
    }


    private ChangeSymbol(result: number = -1, _blur: boolean = false) {
        let newSymbol
        // if (GameManager.GM.FreeGameTimes > 0)
        newSymbol = this.Random(11, 0)
        // }
        // else {
        //     newSymbol = this.Random(Symbol_ID, 0)
        // }

        if (result != -1)
            newSymbol = result;

        this.ColumnData.pop();
        this.ColumnData.splice(0, 0, newSymbol);

        this.updateSymbols(_blur);

    }

    private updateSymbols(_blur: boolean = false) {
        if (_blur) {
            for (let i = 0; i < this.ColumnData.length; i++) {
                this.Column_Symbols[i].getComponentInChildren(cc.Sprite).spriteFrame = this.mblur_Sprite[this.ColumnData[i]]
                this.Column_Symbols[i].anchorY = 0.5
            }

        } else {
            for (let i = 0; i < this.ColumnData.length; i++) {
                this.Column_Symbols[i].getComponentInChildren(cc.Sprite).spriteFrame = this.m_Sprite[this.ColumnData[i]]
                this.Column_Symbols[i].anchorY = 0.5
            }

        }
    }

    public updateSymbols_FreeGamelastResult() {
        for (let i = 1; i < 6; i++) {
            this.Column_Symbols[i].getComponentInChildren(cc.Sprite).spriteFrame = this.m_Sprite[this.ColumnData[i]]
            this.Column_Symbols[i].anchorY = 0.5
        }
    }

    //
    firstGameResult(): number[] {
        let RandonIndexArray: number[] = [];
        for (let i = 0; i < this.Symbol_column_count; i++) {

            RandonIndexArray[i] = this.Random(10, 1)
            this.Column_Symbols[i].getComponentInChildren(cc.Sprite).spriteFrame = this.m_Sprite[RandonIndexArray[i]];
            // this.Column_bubbleData[i] = this.bubbleRandom().toString()
        }
        return RandonIndexArray;//
    }

    Random(max, min) {
        return Math.round(Math.random() * (max - min) + min);
    }




}




