
import GameManager from "../GameManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PotionDigital extends cc.Component {

    // itemIcon: cc.Sprite = null;
    Digital_SpriteFrames: cc.SpriteFrame[] = [];

    @property(cc.Layout)
    DigitalLayout: cc.Layout = null;

    private DigitalString: string = '0';
    private _DigitalValue: number;

    public get DigitalValue(): number {
        return this._DigitalValue;
    }
    public set DigitalValue(v: number) {
        this._DigitalValue = v;
        // if (v >= 10000) {
        //     this.DigitalString = v.toString()
        // }
        // else {
        this.DigitalString = GameManager.toStringThousandth(v)
        // }
        // this.updateNumImgs(this.DigitalString)
        this.node.getChildByName('Layout').getComponent(cc.Label).string = this.DigitalString
    }


    protected onLoad(): void {
        // this.itemIcon = this.node.getChildByName('icon').getComponent(cc.Sprite)
        // this.node.getComponentInChildren(sp.Skeleton).setCompleteListener(() => { this.node.getComponentInChildren(sp.Skeleton).setAnimation(0, 'idle', false) })
        // this.DigitalLayout = this.node.getComponentInChildren(cc.Layout)
        // this.DigitalLayout.type = cc.Layout.Type.HORIZONTAL
        // this.DigitalLayout.resizeMode = cc.Layout.ResizeMode.CONTAINER
        // this.DigitalLayout.spacingX = -20
    }


    private updateNumImgs(valueStr: string) {

        if (this.DigitalLayout.node != null) {
            this.DigitalLayout.node.destroyAllChildren();
            for (const char of valueStr) {
                let charNode = new cc.Node();
                charNode.setParent(this.DigitalLayout.node);
                // charNode.scale = this.bubbleImgScale;
                let spr = charNode.addComponent(cc.Sprite);
                let tex = this.getSpriteByChar(char);
                if (tex) {
                    spr.spriteFrame = tex;
                    spr.sizeMode = cc.Sprite.SizeMode.TRIMMED
                    spr.trim = true;
                    spr.node.anchorY = 0.5
                }
            }
        }
    }

    private getSpriteByChar(char: string): cc.SpriteFrame {

        if (char.match(/[0-9]/)) {
            let num = Number.parseInt(char);
            return this.determineSpr(num)
        }
        else if (char === ',')
            return this.determineSpr(10);
        else if (char === '.')
            return this.determineSpr(11);
        else if (char === 'k')
            return this.determineSpr(12);

        return null;
    }

    private determineSpr(num: number) {
        return this.Digital_SpriteFrames[num]
    }


}
