
export default class ServerDataFormat {


    data: {
        MainResult:
        {
            betNumber: number[],
            LineNumber: number[],
            LineScore: number[],
            LineEffectCount: number[],
            SymbolNumber: number[],
            TotalWin: number,
        },
        FreeResult: FreeResultFormat[],
        TotalWin: number,
        round: string,
        result: { last_credit: number }
    }

    setData(Sdata: Object) {
        this.data = Sdata
    }

}


export class FreeResultFormat {
    Data: {
        betNumber: number[],
        LineNumber: number[],
        LineScore: number[],
        LineEffectCount: number[],
        SymbolNumber: number[],
        TotalWin: number,
        FreeWin: number
    }
}

