import { AudioManager } from "../Audio/AudioManager";
import { SoundType } from "../Audio/AudioType";
import { EventSystem, EventType } from "../EventSystem";
import GameServer from "../FakeServer/GameServer";
import GameManager from "../GameManager";


const { ccclass, property } = cc._decorator;

@ccclass
export default class Bet_Setting_TS extends cc.Component {


    @property(cc.Label)
    title_GameLine: cc.Label = null;

    @property(cc.Button)
    btn_perLine_Add: cc.Button = null;
    @property(cc.Button)
    btn_perLine_Sub: cc.Button = null;
    @property(cc.Button)
    btn_Total_Add: cc.Button = null;
    @property(cc.Button)
    btn_Total_Sub: cc.Button = null;

    @property(cc.Label)
    title_TotalLine: cc.Label = null;

    @property(cc.Button)
    btn_Confirm: cc.Button = null;
    @property(cc.Button)
    btn_close: cc.Button = null;

    @property(cc.Label)
    value_PerLine: cc.Label = null;
    @property(cc.Label)
    value_TotalBet: cc.Label = null;

    BetList: number[] = [];

    private _BetIndex: number = 0;
    public get BetIndex(): number {
        return this._BetIndex;
    }
    public set BetIndex(v: number) {
        let idx = v
        if (idx < 0)
            v = this.BetList.length - Math.abs(idx)
        else
            v = idx % this.BetList.length
        this._BetIndex = v;
    }

    init() {

        // this.btn_perLine_Add.node.on('click', () => {
        //     AudioManager.playSound(SoundType.CLICK_BUTTON)
        //     GameServer.self.addBet();
        //     // this.value_PerLine.string = GameServer.self.getLineBet().toString()
        //     this.value_TotalBet.string = GameManager.toStringThousandth(GameServer.self.TotalBet)
        //     EventSystem.emit(this.node, EventType.UpdateUI)

        // })
        // this.btn_perLine_Sub.node.on('click', () => {
        //     AudioManager.playSound(SoundType.CLICK_BUTTON)
        //     GameServer.self.subBet();
        //     // this.value_PerLine.string = GameServer.self.getLineBet().toString()
        //     this.value_TotalBet.string = GameManager.toStringThousandth(GameServer.self.TotalBet)
        //     EventSystem.emit(this.node, EventType.UpdateUI)
        // })

        this.btn_Total_Add.node.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            this.BetIndex += 1;
            let textTotalBet: number = (this.BetList[this.BetIndex])
            this.value_TotalBet.string = textTotalBet >= 100 ? textTotalBet.toPrecisionStr(0) : textTotalBet.toPrecisionStr(2)
            // GameServer.self.addBet();
            // this.value_PerLine.string = GameServer.self.getLineBet().toString()
            // this.value_TotalBet.string = GameManager.toStringThousandth(GameServer.self.TotalBet)
            EventSystem.emit(this.node, EventType.UpdateUI)
        })
        this.btn_Total_Sub.node.on('click', () => {
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            this.BetIndex -= 1;
            let textTotalBet: number = (this.BetList[this.BetIndex])
            this.value_TotalBet.string = textTotalBet >= 100 ? textTotalBet.toPrecisionStr(0) : textTotalBet.toPrecisionStr(2)
            // GameServer.self.subBet();
            // this.value_PerLine.string = GameServer.self.getLineBet().toString()
            // this.value_TotalBet.string = GameManager.toStringThousandth(GameServer.self.TotalBet)
            EventSystem.emit(this.node, EventType.UpdateUI)
        })

        this.btn_close.node.on('click', () => {
            this.node.parent.getChildByName('tgl_bet').getComponent(cc.Toggle).isChecked = false;
            this.node.parent.getChildByName('Page_Bet()').active = false;
            AudioManager.playSound(SoundType.CLICK_BUTTON)
        })
        this.btn_Confirm.node.on('click', () => {
            this.node.parent.getChildByName('tgl_bet').getComponent(cc.Toggle).isChecked = false
            this.node.parent.getChildByName('Page_Bet()').active = false;
            AudioManager.playSound(SoundType.CLICK_BUTTON)
            GameServer.self.setBet(this.BetIndex)
            // GameManager.GM.UI_BuyFreeGame.update_BuyFreeSpinPrice();
        })

    }

    protected lateUpdate(dt: number): void {
        // this.value_TotalBet.string = GameManager.toStringThousandth(GameServer.self.TotalBet)
    }
    protected onEnable(): void {
        this.BetList = GameServer.self.Bet_list
        this.BetIndex = GameServer.self.BetIdx
        let textTotalBet: number = (this.BetList[this.BetIndex])
        this.value_TotalBet.string = textTotalBet >= 100 ? textTotalBet.toPrecisionStr(0) : textTotalBet.toPrecisionStr(2)

    }



}
